import React, { Component, Fragment } from 'react'
import PropTypes from 'prop-types'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import { Route, Switch, Redirect } from 'react-router-dom'
import { sortBy } from 'lodash'

import GridView from '../Shared/GridView'
import StationCarousel from '../Shared/StationCarousel'
import SpatialNavigationWrapper from '../Shared/SpatialNavigationWrapper'
import LoadingSpinner from '../Shared/LoadingSpinner'
import MusicNavHeader from '../MusicNavHeader'

import {
  MUSIC_HEADER_ID,
  MUSIC_GRID_VIEW_ID,
  ALL_MUSIC_FIRST_CAROUSEL
} from '../../utils/constants'
import { genreItems, injectSpatialNavId } from '../../utils/modules'

import { SpatialNavigation } from '../../utils/spatial-nav'

import * as musicAction from '../../actions/music'
import * as musicActionCreator from '../../asyncActionCreators/music'

import './genres-page.scss'

class GenresPageComponent extends Component {
  constructor(props) {
    super(props)

    const orderedSwimLanes = this.setUpGenresSpatialNav()
    props.requestAllGenreModules()
    props.saveMusicGenreList(orderedSwimLanes)
  }

  setUpGenresSpatialNav = () => {
    const allGenreLanes = genreItems.slice(1)
    const formattedGenreLanes = injectSpatialNavId(allGenreLanes)
    const orderedSwimlanes = sortBy(formattedGenreLanes, 'name')

    return orderedSwimlanes
  }

  render() {
    const {
      isLoading,
      musicGenre,
      genreCategories,
      defaultGenre = Object.keys(musicGenre).sort()[0],
      match: { url }
    } = this.props

    return (
      <Fragment>
        {isLoading ? (
          <LoadingSpinner />
        ) : (
          <Fragment>
            <MusicNavHeader hideAll={!url.includes('music')} />
            <div id="music-categeory-container">
              <Switch>
                <Route
                  exact
                  path={`${url}`}
                  render={() => <Redirect to={`${url}/${defaultGenre}`} />}
                />
                <Route
                  exact
                  path={`${url}/All`}
                  render={() => (
                    <AllMusicStations
                      musicGenre={musicGenre}
                      genreCategories={genreCategories}
                    />
                  )}
                />
                <Route
                  path={`${url}/:genreType`}
                  render={props => {
                    return <StationByGenre musicGenre={musicGenre} {...props} />
                  }}
                />
              </Switch>
            </div>
          </Fragment>
        )}
      </Fragment>
    )
  }
}
const StationByGenreWithNav = SpatialNavigationWrapper({
  defaultElement: MUSIC_HEADER_ID,
  previousComponentMap: {
    [MUSIC_GRID_VIEW_ID]: MUSIC_HEADER_ID
  }
})

const StationByGenre = ({
  musicGenre,
  match: {
    params: { genreType }
  }
}) => (
  <StationByGenreWithNav>
    <GridView navId={MUSIC_GRID_VIEW_ID} stations={musicGenre[genreType]} />
  </StationByGenreWithNav>
)

const AllMusicStationsWithNav = SpatialNavigationWrapper({
  defaultElement: MUSIC_HEADER_ID,
  previousComponentMap: {
    [ALL_MUSIC_FIRST_CAROUSEL]: MUSIC_HEADER_ID
  }
})

const AllMusicStations = ({ genreCategories, musicGenre }) => {
  const swimlanes = genreCategories.map(swimlane => {
    const module = musicGenre[swimlane.slug]
    return (
      <StationCarousel
        navId={swimlane.navId}
        key={`station-carousel-${swimlane.navId}`}
        id={swimlane.navId}
        headerText={swimlane.name}
        stations={module}
      />
    )
  })
  return (
    <div className="all-music-container">
      <AllMusicStationsWithNav>{swimlanes}</AllMusicStationsWithNav>
    </div>
  )
}

GenresPageComponent.propTypes = {
  isLoading: PropTypes.bool,
  musicGenre: PropTypes.object,
  genreCategories: PropTypes.array
}

GenresPageComponent.defaultProps = {
  isLoading: true,
  musicGenre: {},
  genreCategories: []
}

const mapStateToProps = ({
  music: { isLoading, musicGenre, genreCategories }
}) => {
  return {
    isLoading,
    musicGenre,
    genreCategories
  }
}

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      requestAllGenreModules: musicActionCreator.requestAllGenreModules,
      saveMusicGenreList: musicAction.saveMusicGenreList
    },
    dispatch
  )
}

const GenresPage = connect(
  mapStateToProps,
  mapDispatchToProps
)(GenresPageComponent)

export { GenresPageComponent }
export default GenresPage
