import axios from 'axios'
import { API_BASE_URL, GEO_API_BASE_URL, DEFAULT_ZIPCODE } from './constants'
import { sortBy } from 'lodash'
import { isModuleValid } from './modules'
import $badgerReady from './money-badger-wrapper'

export const getStationsByGenreId = id => {
  return axios.get(
    `${API_BASE_URL}/stations/?filter[genre_id]=${id}&page[size]=1000&sort=popularity`
  )
}

export const getModulesByID = id => {
  return axios.get(`${API_BASE_URL}/modules/${id}`)
}

export const getModulesByPage = async (pageId = 1) => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/modules?sort=sort_order&filter[page_id]=${pageId}&page[size]=10&page[number]=1`
    )
    const local = await getLocalStations()

    const modules = response.data.data
      .map(module => {
        if (module.attributes.type === 'local_station_carousel') {
          module.attributes.destinations = local
        }
        return module
      })
      .filter(module => {
        const valid = isModuleValid(module)
        return valid
      })
    return {
      modules
    }
  } catch (err) {
    console.log(err)
  }
}

export const getStations = async () => {
  const newsTalk = await getNewsStations()
  const sports = await getSportsStations()
  return { ...newsTalk, ...sports }
}

export const getNewsStations = async () => {
  try {
    const newsResponse = await axios.get(
      `${API_BASE_URL}/stations/?filter[category]=News&page[size]=10`
    )
    const newsStations = newsResponse.data.data
    const talkResponse = await axios.get(
      `${API_BASE_URL}/stations/?filter[category]=Talk&page[size]=10`
    )
    const talkStations = talkResponse.data.data

    return {
      newsTalk: [...talkStations, ...newsStations]
    }
  } catch (err) {
    console.log(err)
  }
}

export const getSportsStations = async () => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/stations/?filter[category]=Sports&page[size]=10`
    )
    const sports = response.data.data

    return {
      sports
    }
  } catch (err) {
    console.log(err)
  }
}

export const getLocalStations = async () => {
  try {
    const zipcode = await retrieveBoxZipCode()
    const marketId = await getMarketIdbyZipcode({ zipcode })
    const response = await requestStationsByMarketId({ marketId })
    return response.data.data
  } catch (err) {
    //if there are error retrieving any box-specific information, we retrieve
    //localStations based on default national ID 14
    const response = await requestStationsByMarketId({ marketId: 14 })
    return response.data.data
  }
}

const retrieveBoxZipCode = () =>
  new Promise(async (resolve, reject) => {
    const $badger = await $badgerReady
    const resolveZipcode = ({ zipcode }) => resolve(zipcode)
    const resolveDefaultZipcode = () => resolve(DEFAULT_ZIPCODE)
    $badger
      .info()
      .success(resolveZipcode)
      .failure(resolveDefaultZipcode)
  })

export const getFeaturedStations = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/modules/?page[size]=100`)
    const featuredStations = response.data.data
    return {
      featuredStations
    }
  } catch (err) {
    console.log(err)
  }
}

export const getMarkets = () =>
  axios.get(`${API_BASE_URL}/markets/?sort=name&page[size]=1000&page[number]=1`)

export const requestStationsByMarketId = ({ marketId }) =>
  axios.get(
    `${API_BASE_URL}/stations?sort=-popularity&page[size]=1000&page[number]=1&filter[market_id]=${marketId}`
  )

export const getGenres = async ({ filters = '' }) => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/genres/?page[size]=100&${filters}`
    )
    const genreStations = response.data.data
    const genres = sortBy(genreStations, genre => {
      return genre.attributes.sort_order
    })
    return { genres }
  } catch (err) {
    console.log(err)
  }
}

export const getStationsBySearchTerm = async query => {
  try {
    const response = await axios.get(
      `${API_BASE_URL}/stations?page[size]=25&page[number]=1&q=${query}`
    )
    const searchStations = response.data.data
    return {
      searchStations
    }
  } catch (err) {
    console.log(err)
  }
}

export const getMarketIdbyZipcode = async ({ zipcode }) => {
  try {
    const response = await axios.get(
      `${GEO_API_BASE_URL}/markets?zipcode=${zipcode}`
    )
    const { Markets: markets } = response.data
    const marketIds = markets.map(market => market.id)
    return marketIds
  } catch (err) {
    console.log(err)
    return [14]
  }
}
