import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import { slice, sortBy } from 'lodash'

import DefaultButton from '../Shared/Buttons'
import { genreItems } from '../../utils/modules'
import {
  scrollNav,
  focusEnteredComponent,
  blurEnteredComponent,
  focusOnDefaultElement
} from '../../utils/spatial-nav'

import { MUSIC_HEADER_ID } from '../../utils/constants'

import * as musicAction from '../../actions/music'
import * as appActionCreator from '../../asyncActionCreators/app'

import NavigationMap from '../../factories/navigationFactory'

const MusicNavContainer = styled.div`
  position: relative;
  z-index: 100;
`
export const NavButton = styled(DefaultButton)`
  border-color: transparent;
  background-color: ${p => p.theme.colors.btnBackgroundActive};
  color: white;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  width: 10.75rem;
  padding: 0;
  &:not(first-child) {
    margin-right: 0.5rem;
    margin-left: 0.5rem;
  }
  &.selected {
    :after {
      content: '';
      position: absolute;
      bottom: 0.468rem;
      left: 50%;
      transform: translateX(-50%);
      background: ${p => p.theme.colors.primaryLight};
      height: 0.125rem;
      border-radius: 10px;
      width: 0.75rem;
    }
  }
`

const MusicNav = styled.div`
  margin-left: 5.25rem;
  position: fixed;
  left: 0;
  display: inline-flex;
  white-space: nowrap;
  width: max-content;
  align-items: flex-start;
  background-color: ${p => p.theme.colors.secondaryDark}
  & :first-child {
    margin-left: 0;
  }
  &:focus-within {
    ${NavButton} {
      background-color: ${p => p.theme.colors.btnBackgroundActive};
      &:focus {
        background-color: ${p => p.theme.colors.btnBackgroundFocus};
      }
    }
  }
`

class MusicNavHeader extends PureComponent {
  state = {
    genreNavButtons: []
  }

  componentDidMount() {
    this.getNavButtons()
  }

  componentWillUnmount() {
    blurEnteredComponent(MUSIC_HEADER_ID)
    this.props.resetFilterStationsByGenres()
  }

  getNavButtons = () => {
    let navItems = genreItems
    const { hideAll, filterStationsByGenres } = this.props

    if (hideAll) {
      navItems = slice(genreItems, 1)
      navItems = sortBy(navItems, genre => genre.name)
    }

   hideAll && filterStationsByGenres(navItems[0].name)

    this.setState(
      state => ({ genreNavButtons: navItems }),
      () => {
        focusEnteredComponent({ cmpNavId: MUSIC_HEADER_ID })
        focusOnDefaultElement('.selected')
      }
    )
  }

  handleClick = genreItem => {
    const {
      currentPage,
      parentPage,
      parentPageKey,
      onNavigate,
      filterStationsByGenres
    } = this.props
    const to = new NavigationMap({
      pathURL: `${genreItem.slug}`,
      currentPage,
      parentPage,
      parentPageKey
    })

    onNavigate(to)
    filterStationsByGenres(genreItem.name)
  }

  render() {
    const { genreFilter, hideAll = false } = this.props

    return (
      <MusicNavContainer className="nav-bar">
        <MusicNav id={MUSIC_HEADER_ID}>
          {this.state.genreNavButtons.map(genreItem => (
            <NavButton
              className={`${
                genreFilter === genreItem.name ? 'selected' : ''
              } focusable focusable-${MUSIC_HEADER_ID}`}
              onFocus={scrollNav}
              key={genreItem.name}
              onClick={() => {
                this.handleClick(genreItem)
              }}
            >
              {genreItem.name}
            </NavButton>
          ))}
        </MusicNav>
      </MusicNavContainer>
    )
  }
}

MusicNavHeader.propTypes = {
  filterStationsByGenres: PropTypes.func,
  resetFlterStationsByGenres: PropTypes.func
}

MusicNavHeader.defaultProps = {}

const mapStateToProps = ({
  music: { genreFilter },
  app: { currentPage, parentPage, parentPageKey }
}) => {
  return { genreFilter, currentPage, parentPage, parentPageKey }
}
const mapDispatchToProps = dispatch => {
  return {
    filterStationsByGenres: data =>
      dispatch(musicAction.filterStationsByGenres(data)),
    resetFilterStationsByGenres: () =>
      dispatch(musicAction.resetFilterStationsByGenres()),
    onNavigate: data => dispatch(appActionCreator.onNavigate(data))
  }
}

const MusicNavHeaderWithRouter = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(MusicNavHeader)
)
export default MusicNavHeaderWithRouter

export { MusicNavHeader }
