export const debounceEvent = (timeout, eventToDebounce) => {
  let dispatching = false

  const debounce = e => {
    if (!dispatching) {
      dispatching = true
      setTimeout(() => {
        dispatching = false
      }, timeout)
    } else {
      e.preventDefault()
    }
  }

  window.addEventListener(eventToDebounce, debounce)
}
