import React, { PureComponent } from 'react'
import { SEARCH_KEYPAD_ID } from '../../utils/constants'
import SpatialNavigationWrapper from '../Shared/SpatialNavigationWrapper'
import './keypad.scss'

export const buildKey = (value, onKeyPress, className = '') => {
  return (
    <button
      name={value}
      onClick={onKeyPress}
      key={value}
      value={value}
      className={`focusable-search-keypad focusable key ${className}`}
    >
      {value}
    </button>
  )
}

const Keypad = props => {
  const {
    onKeyPress,
    onDeleteKeyPress,
    onSpaceKeyPress,
    onClearKeyPress
  } = props
  return (
    <div className="keypad-static">
      {buildKey('a', onKeyPress, 'A')}
      {buildKey('b', onKeyPress, 'B')}
      {buildKey('c', onKeyPress, 'C')}
      {buildKey('d', onKeyPress, 'D')}
      {buildKey('e', onKeyPress, 'E')}
      {buildKey('f', onKeyPress, 'F')}
      {buildKey('g', onKeyPress, 'G')}
      {buildKey('h', onKeyPress, 'H')}
      {buildKey('i', onKeyPress, 'I')}
      {buildKey('j', onKeyPress, 'J')}
      {buildKey('k', onKeyPress, 'K')}
      {buildKey('l', onKeyPress, 'L')}
      {buildKey('m', onKeyPress, 'M')}
      {buildKey('n', onKeyPress, 'N')}
      {buildKey('o', onKeyPress, 'O')}
      {buildKey('p', onKeyPress, 'P')}
      {buildKey('q', onKeyPress, 'Q')}
      {buildKey('r', onKeyPress, 'R')}
      {buildKey('s', onKeyPress, 'S')}
      {buildKey('t', onKeyPress, 'T')}
      {buildKey('u', onKeyPress, 'U')}
      {buildKey('v', onKeyPress, 'V')}
      {buildKey('w', onKeyPress, 'W')}
      {buildKey('x', onKeyPress, 'X')}
      {buildKey('y', onKeyPress, 'Y')}
      {buildKey('z', onKeyPress, 'Z')}
      {buildKey('0', onKeyPress, '0')}
      {buildKey('1', onKeyPress, '1')}
      {buildKey('2', onKeyPress, '2')}
      {buildKey('3', onKeyPress, '3')}
      {buildKey('4', onKeyPress, '4')}
      {buildKey('5', onKeyPress, '5')}
      {buildKey('6', onKeyPress, '6')}
      {buildKey('7', onKeyPress, '7')}
      {buildKey('8', onKeyPress, '8')}
      {buildKey('9', onKeyPress, '9')}
      {buildKey('Space', onSpaceKeyPress, 'bottom-keys')}
      {buildKey('Delete', onDeleteKeyPress, 'bottom-keys')}
      {buildKey('Clear', onClearKeyPress, 'bottom-keys')}
      {buildKey('.', onKeyPress, 'preiod')}
    </div>
  )
}

class KeypadStaticWithNav extends PureComponent {
  render() {
    const SpatialNav = SpatialNavigationWrapper({
      enableHorizontalScroll: true
    })
    return (
      <SpatialNav>
        <Keypad {...this.props} navId={SEARCH_KEYPAD_ID} />
      </SpatialNav>
    )
  }
}

export default KeypadStaticWithNav
export { Keypad }
