import React, { Fragment, memo } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import * as appActionCreator from '../../../asyncActionCreators/app'
import { showPlayer } from '../../../actions/app'

import {
  StationCardContent,
  CardContentText,
  CardLabelHeader,
  StationImage,
  CardContainer
} from './CardStyledComponents'

import NavigationMap from '../../../factories/navigationFactory'
import { playSelectedStation } from '../../../utils/player'

import CARD_IMGURL from '../../../assets/demo/station@3x.png'

export const StationCard = props => {
  const { cardType = 'square', scrollToCard } = props

  const cardComponents = {
    circle: CircleCard,
    square: SquareCard
  }
  const Card = cardComponents[cardType]
  return <Card {...props} imageFocus={scrollToCard} />
}

StationCard.propTypes = {
  focusProp: PropTypes.string.isRequired,
  cardType: PropTypes.string.isRequired,
  cardId: PropTypes.number.isRequired,
  parentCarousel: PropTypes.string.isRequired,
  cardPosition: PropTypes.number.isRequired,
  cardLabel: PropTypes.string,
  imgUrl: PropTypes.string
}

export const SquareCardComponent = memo(props => {
  const {
    focusProp,
    cardType,
    imageFocus,
    cardLabel = 'Station Name',
    imgUrl,
    cardId,
    parentCarousel,
    cardPosition,
    showPlayer
  } = props

  const handleOnClick = cardId => {
    playSelectedStation(cardId)
    showPlayer()
  }

  return (
    <CardContainer
      id={`${cardId}_${parentCarousel}`}
      className={`${focusProp} focusable ${cardType}`}
      onFocus={imageFocus}
      onClick={() => handleOnClick(cardId)}
      headerLength={cardLabel.length}
      data-parentcarousel={parentCarousel}
      data-cardposition={cardPosition}
    >
      <Fragment>
        <StationImage
          imgUrl={`${imgUrl}?width=172`}
          title="Radio Station Image"
          className={cardType}
        />
        <StationCardContent className="card-content">
          {cardLabel.length > 14 ? (
            <Fragment>
              <CardLabelHeader className="scroll">{cardLabel}</CardLabelHeader>
              <CardLabelHeader className="scroll">{cardLabel}</CardLabelHeader>
              <CardLabelHeader className="scroll">{cardLabel}</CardLabelHeader>
              <CardLabelHeader className="scroll">{cardLabel}</CardLabelHeader>
            </Fragment>
          ) : (
            <CardLabelHeader>{cardLabel}</CardLabelHeader>
          )}
        </StationCardContent>
      </Fragment>
    </CardContainer>
  )
})

SquareCardComponent.propTypes = {
  imageFocus: PropTypes.func.isRequired
}

export const CircleCardComponent = memo(props => {
  const {
    focusProp,
    cardType,
    imageFocus,
    imgUrl = CARD_IMGURL,
    cardLabel = 'Station Name',
    cardId,
    onNavigate,
    parentCarousel,
    cardPosition
  } = props

  const navigationObject = new NavigationMap({
    pathURL: `/category/${cardId}`,
    showPageLabel: true,
    currentPage: cardLabel,
    parentPage: 'discover',
    parentPageKey: 'discover'
  })

  return (
    <CardContainer
      id={`${cardId}_${parentCarousel}`}
      className={`${focusProp} focusable ${cardType}`}
      onFocus={imageFocus}
      onClick={() => onNavigate(navigationObject)}
      data-parentcarousel={parentCarousel}
      data-cardposition={cardPosition}
    >
      <StationImage
        imgUrl={`${imgUrl}?width=172`}
        title="Radio Station Image"
        className={cardType}
      >
        <CardContentText className={cardType}>{cardLabel}</CardContentText>
      </StationImage>
    </CardContainer>
  )
})

const mapDispatchToProps = dispatch => {
  return {
    onNavigate: data => dispatch(appActionCreator.onNavigate(data)),
    showPlayer: () => dispatch(showPlayer())
  }
}
const CircleCard = connect(
  null,
  mapDispatchToProps
)(CircleCardComponent)

const SquareCard = connect(
  null,
  mapDispatchToProps
)(SquareCardComponent)

export { CircleCard }

export default StationCard
