import React, { useState, useMemo, useEffect, useRef } from 'react'
import styled from 'styled-components'
import { connect } from 'react-redux'
import {
  KEYPRESS_ENTER,
  KEYPRESS_ARROW_DOWN,
  JUMBO_CATEGORY_ID,
  SEARCH_NAV_ID,
  SETTINGS_PAGE_ID,
  MUSIC_HEADER_ID,
  MARKET_HEADER_ID,
  PLAYER_CONTAINER_ID
} from '../../utils/constants'
import {
  makeNavFocusable,
  blurEnteredComponent,
  SpatialNavigation
} from '../../utils/spatial-nav'
import {
  NavigateDiscovery,
  NavigateSearch,
  NavigateSettings
} from '../../utils/navigation-map'

import RadioPlayer from '../RadioPlayer'

import * as asyncAppAction from '../../asyncActionCreators/app'

import './nav-header.scss'
import logo from '../../assets/radiocom-logo-text.svg'

/* istanbul ignore next */
export const NavLogo = styled.img`
  width: 13.25rem;
  position: fixed;
  top: 64px;
  left: 5.25rem;
`
/* istanbul ignore next */
export const NavBar = styled.ul`
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  margin-bottom: 3.375rem;
  & :not(:last-child) {
    margin-right: 2.875rem;
  }
`
/* istanbul ignore next */
export const NavItem = styled.li`
  flex-shrink: 0;
  color: ${p => p.theme.colors.primaryLight};
  font-size: 1.125rem;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  font-family: ${p => p.theme.fonts.bold}
  list-style: none;

  &:hover {
    cursor: pointer;
  }
  
  &:focus {
    transform: scale(1.222);
    outline: none;
    :after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      background: ${p => p.theme.colors.primaryLight};
      height: 0.125rem;
      border-radius: 10px;
    }
  }
`

export const Title = styled.h1`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  color: ${p => p.theme.colors.primaryLight};
  font-size: 1.375rem;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  font-family: ${p => p.theme.fonts.bold}
  margin: 0;
`

const NavHeaderComponent = props => {
  const {
    onNavigate,
    parentPage,
    currentPage,
    isPlayerVisible,
    showSplashScreen
  } = props
  const [isNavBlurred, setIsNavBlurred] = useState(false)
  const [focusedElement, setFocusedElement] = useState(currentPage)
  const discoverRef = useRef()

  useEffect(() => setIsNavBlurred(true), [])

  useEffect(() => {
    if (parentPage === null) {
      setFocusedElement(currentPage)
      // appropriately hacky but works for now
      // revisit this when addressing default focus
      currentPage === 'Discover' && discoverRef.current.focus()

      setIsNavBlurred(true)
    }
  }, [currentPage])

  const navigateDownMap = {
    Discover: `@${JUMBO_CATEGORY_ID}`,
    Search: `@${SEARCH_NAV_ID}`,
    Settings: `@${SETTINGS_PAGE_ID}`,
    'Browse By City': `@${MARKET_HEADER_ID}`,
    'Browse By Genre': `@${MUSIC_HEADER_ID}`,
    Music: `@${MUSIC_HEADER_ID}`
  }

  useEffect(() => {
    !showSplashScreen &&
      SpatialNavigation.set('nav-bar', {
        leaveFor: {
          right: isPlayerVisible ? `@${PLAYER_CONTAINER_ID}` : '',
          down: navigateDownMap[currentPage]
        }
      })
  }, [currentPage, isPlayerVisible])

  useEffect(() => {
    makeNavFocusable('.focushere')

    return () => {
      blurEnteredComponent('nav-bar')
    }
  }, [])

  const handleKeyPress = useMemo(
    () => (key, to) => {
      if (key === KEYPRESS_ENTER && to.currentPage !== currentPage) {
        setFocusedElement(to.currentPage)
        onNavigate(to)
        !isNavBlurred && setIsNavBlurred(!isNavBlurred)
      }
    },
    [currentPage, isNavBlurred]
  )

  let focus = parentPage ? parentPage : focusedElement
  let classToUse = parentPage ? 'nav-hidden' : 'nav-blur'

  return (
    <div id="nav" className="nav-bar nav-container">
      <NavLogo src={logo} />
      <NavBar
        navId="nav"
        id="nav-bar"
        className={`${isNavBlurred && classToUse}`}
        onFocus={() => isNavBlurred && setIsNavBlurred(!isNavBlurred)}
        onKeyDown={e => {
          if (e.key === KEYPRESS_ARROW_DOWN && !isNavBlurred) {
            setIsNavBlurred(!isNavBlurred)
          }
        }}
      >
        <NavItem
          className={`nav-item nav-link ${focus === 'Discover' && 'focushere'}`}
          onKeyPress={e => handleKeyPress(e.key, NavigateDiscovery)}
          ref={discoverRef}
        >
          Discover
        </NavItem>
        <NavItem
          className={`nav-item nav-link ${focus === 'Search' && 'focushere'}`}
          onKeyPress={e => handleKeyPress(e.key, NavigateSearch)}
        >
          Search
        </NavItem>
        <NavItem
          className={`nav-item nav-link ${focus === 'Settings' && 'focushere'}`}
          onKeyPress={e => handleKeyPress(e.key, NavigateSettings)}
        >
          Settings
        </NavItem>
      </NavBar>
      <RadioPlayer
        className={`focusable-playerContainer`}
        isPlayerVisible={isPlayerVisible}
      />
      <Title className={`${(!isNavBlurred || !parentPage) && 'nav-hidden'}`}>
        {props.currentPage}
      </Title>
    </div>
  )
}

const mapStateToProps = ({ app: { isPlayerVisible, showSplashScreen } }) => {
  return {
    isPlayerVisible,
    showSplashScreen
  }
}

const NavHeader = connect(mapStateToProps)(NavHeaderComponent)

export { NavHeader }
export default NavHeaderComponent
