import {
  NAV_HEADER_ID,
  CAROUSEL_ITEM_SIZE,
  GRIDVIEW_ROW_HEIGHT
} from './constants'
import '../vendor/spatial-navigation'

export const SpatialNavigation = window.SpatialNavigation

export const updateSpatialNavigationSection = ({
  sectionName,
  config = {}
}) => {
  SpatialNavigation.set(sectionName, config)
}

export const initializeSpatialNav = () => {
  SpatialNavigation.init()
}

export const focusOnDefaultElement = (sectionId = '') => {
  SpatialNavigation.makeFocusable()

  // Focus the first navigable element on page.
  SpatialNavigation.focus(sectionId)
}

export const makeNavFocusable = (focusedElement = '.nav-item') => {
  SpatialNavigation.add(`nav-bar`, {
    selector: '.nav-item',
    enterTo: 'default-element',
    defaultElement: focusedElement,
    leaveFor: {
      right: '',
      left: '',
      down: '@jumbo-categories'
    }
  })
  SpatialNavigation.makeFocusable('nav-bar')
}

export const makeItemsFocusable = ({
  cmpNavIds,
  previousComponentMap = undefined,
  nextComponentMap = undefined,
  enableHorizontalScroll = false,
  enableVerticalScroll = true,
  defaultElement
}) => {
  //TODO: Using the previous item to navigate back to. The previous carousel will likely be off screen
  // As such we need to specify where to navigate up to. We should find a better way to do this.
  cmpNavIds.forEach(navId => {
    const nextComponent = nextComponentMap ? nextComponentMap[navId] : undefined
    const previousComponent = previousComponentMap
      ? previousComponentMap[navId]
      : NAV_HEADER_ID
    const selector = `.focusable-${navId}`
    const options = {
      selector: selector,
      enterTo: defaultElement ? 'default-element' : 'last-focused',
      defaultElement: defaultElement,
      leaveFor: {
        up: enableVerticalScroll ? `@${previousComponent}` : '',
        right: enableHorizontalScroll ? undefined : '',
        left: enableHorizontalScroll ? undefined : '',
        down: enableVerticalScroll ? `@${nextComponent}` : ''
      }
    }
    SpatialNavigation.add(navId, options)
  })
  SpatialNavigation.makeFocusable()
  defaultElement && SpatialNavigation.focus(defaultElement)
}

export const makeItemsBlurred = ({ cmpNavIds }) => {
  cmpNavIds.forEach(cmpNavId => {
    SpatialNavigation.remove(cmpNavId)
  })
}

export const focusEnteredComponent = ({
  cmpNavId,
  leftCmp = '',
  rightCmp = ''
}) => {
  const selector = `.focusable-${cmpNavId}`

  const options = {
    selector: selector,
    enterTo: 'default-element',
    defaultElement: '.selected',

    leaveFor: {
      right: rightCmp,
      left: leftCmp
    }
  }
  SpatialNavigation.add(cmpNavId, options)
}

export const blurEnteredComponent = componentId => {
  SpatialNavigation.remove(componentId)
}

export const scrollToSection = ({ target }) => {
  const focusContainer = target.parentNode.id
  const focusElement = document.getElementById(focusContainer)
  setTargetVerticalFocus({
    focusElement
  })
}

export const scrollToCard = ({ target }) => {
  const focusElement = document.querySelector(
    `[data-carouselname='${target.dataset.parentcarousel}']`
  )

  setTargetVerticalFocus({
    focusElement
  })

  setTargetHorizontalFocus({
    focusElement,
    target
  })
}

export const setTargetVerticalFocus = ({ focusElement }) => {
  const windowVerticalCenter = focusElement.offsetParent.offsetTop
  const navBarOffset = document.getElementById('nav').offsetHeight
  const subNav = document.getElementById('music-nav-header')
  const subNavOffset = subNav ? subNav.offsetHeight : 0
  const verticalOffset = windowVerticalCenter - navBarOffset - subNavOffset

  window.scrollTo(0, verticalOffset)
}

export const scrollGridView = ({ target }) => {
  const index = target.dataset.cardposition
  const scrollElement = document.getElementsByClassName('virtual-grid')[0]

  scrollElement.scrollTop = GRIDVIEW_ROW_HEIGHT * index
}

export const setTargetHorizontalFocus = ({ focusElement, target }) => {
  const index = target.dataset.cardposition
  const scrollElement = document.getElementsByClassName(
    `list-${target.dataset.parentcarousel}`
  )[0]

  scrollElement.scrollLeft = CAROUSEL_ITEM_SIZE * index
}

export const scrollNav = ({ target }) => {
  const focusElement = target.parentNode
  focusElement.style.left = `-${target.offsetLeft}px`
  target.scrollIntoView({ inline: 'center' })
}

export const pauseSpatialNav = () => {
  SpatialNavigation.pause()
}

export const resumeSpatialNav = () => {
  SpatialNavigation.resume()
}
