import { replace } from 'connected-react-router'
import * as errors from '../actions/errors'

export const onError = (data, params = []) => dispatch => {
  dispatch(errors.onError(data, params))
  dispatch(replace('/error'))
}

export const onRetry = data => dispatch => {
  dispatch(errors.onCancel())
  dispatch(errors.onRetry())
  dispatch(data.retryFn(data.retryParams))
}

export const onCancel = () => dispatch => {
  dispatch(errors.onCancel())
}
