import {
  GET_MUSIC_STATIONS_BY_ID,
  GET_MUSIC_STATIONS_BY_ID_SUCCESS,
  GET_MUSIC_STATIONS_BY_ID_FAILED,
  FILTER_STATIONS_BY_GENRES,
  RESET_FILTER_STATIONS_BY_GENRES,
  SAVE_MUSIC_GENRE_LIST
} from '../utils/constants'

export const resetFilterStationsByGenres = () => ({
  type: RESET_FILTER_STATIONS_BY_GENRES
})
export const filterStationsByGenres = data => ({
  type: FILTER_STATIONS_BY_GENRES,
  data
})
export const saveMusicGenreList = data => ({
  type: SAVE_MUSIC_GENRE_LIST,
  data
})

export const getMusicStationsByGenreId = () => ({
  type: GET_MUSIC_STATIONS_BY_ID
})

export const getMusicStationsByGenreIdSuccess = data => ({
  type: GET_MUSIC_STATIONS_BY_ID_SUCCESS,
  data
})

export const getMusicStationsByGenreIdFailed = data => ({
  type: GET_MUSIC_STATIONS_BY_ID_FAILED,
  data
})
