import React, { Component, Fragment } from 'react'
import * as SpatialNavHelper from '../../../utils/spatial-nav'
import { difference } from 'lodash'

const SpatialNavigationWrapper = (options = {}) => {
  return class extends Component {
    constructor(props) {
      super(props)
      this.cmpNavIds = this.formatComponentId(props)
    }

    formatComponentId = ({ children }) =>
      this.transformChildren({ children }).map(cmp => cmp.props.navId)

    // if this wrapper is used around a non iterable instance
    // this function returns an iterable instance
    transformChildren = ({ children }) => {
      return Array.isArray(children) ? children : [children]
    }

    componentDidMount() {
      SpatialNavHelper.makeItemsFocusable({
        cmpNavIds: this.cmpNavIds,
        ...options
      })
    }

    componentDidUpdate(prevProp) {
      const prevCmpNavIds = this.formatComponentId(prevProp)
      const newCmpNavIds = this.formatComponentId(this.props)
      const addedSwimLanes = difference(newCmpNavIds, prevCmpNavIds)
      const removedSwimLanes = difference(prevCmpNavIds, newCmpNavIds)

      const prevCompIndexValue = newCmpNavIds.indexOf(addedSwimLanes[0]) - 1
      const previousComponent = newCmpNavIds[prevCompIndexValue]

      removedSwimLanes.length > 0 &&
        SpatialNavHelper.makeItemsBlurred({
          cmpNavIds: removedSwimLanes
        })

      addedSwimLanes.length > 0 &&
        SpatialNavHelper.makeItemsFocusable({
          cmpNavIds: addedSwimLanes,
          previousComponent,
          ...options
        })
    }

    componentWillUnmount() {
      const cmpNavIds = this.formatComponentId(this.props)

      Array.isArray(cmpNavIds)
        ? SpatialNavHelper.makeItemsBlurred({
            cmpNavIds
          })
        : SpatialNavHelper.blurEnteredComponent(this.cmpNavIds)
    }

    render() {
      return <Fragment> {this.props.children} </Fragment>
    }
  }
}

export default SpatialNavigationWrapper
