import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

import DefaultButton from '../Shared/Buttons'
import NavigationMap from '../../factories/navigationFactory'

import {
  scrollNav,
  focusEnteredComponent,
  blurEnteredComponent,
  updateSpatialNavigationSection
} from '../../utils/spatial-nav'

import { MARKET_HEADER_ID } from '../../utils/constants'

import * as marketActionCreators from '../../asyncActionCreators/market'
import * as appActionCreator from '../../asyncActionCreators/app'

const MarketNavContainer = styled.div`
  position: relative;
  z-index: 100;
`
export const NavButton = styled(DefaultButton)`
  border-color: transparent;
  background-color: ${p => p.theme.colors.btnBackgroundActive};
  color: white;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  width: 10.75rem;
  padding: 0;
  &:not(first-child) {
    margin-right: 0.5rem;
    margin-left: 0.5rem;
  }
  &:after {
    content: ${p => (p.isSelected ? `''` : '')};
    position: absolute;
    bottom: 0.468rem;
    left: 50%;
    transform: translateX(-50%);
    background: ${p => p.theme.colors.primaryLight};
    height: 0.125rem;
    border-radius: 10px;
    width: 0.75rem;
  }
`

const MarketNav = styled.div`
  margin-left: 5.25rem;
  position: fixed;
  left: 0;
  display: inline-flex;
  white-space: nowrap;
  width: max-content;
  align-items: flex-start;
  background-color: ${p => p.theme.colors.secondaryDark}
  & :first-child {
    margin-left: 0;
  }
  &:focus-within {
    ${NavButton} {
      background-color: ${p => p.theme.colors.btnBackgroundActive};
      &:focus {
        background-color: ${p => p.theme.colors.btnBackgroundFocus};
      }
    }
  }
`
class MarketNavHeader extends PureComponent {
  constructor(props) {
    super(props)
    props.requestAllMarkets()
  }
  componentDidMount() {
    focusEnteredComponent({ cmpNavId: MARKET_HEADER_ID })
  }

  componentWillUnmount() {
    blurEnteredComponent(MARKET_HEADER_ID)
  }

  handleClick = e => {
    const { id } = e.target
    const to = new NavigationMap({
      pathURL: `/search/market/${id}`,
      currentPage: 'Browse By City',
      parentPage: 'search',
      parentPageKey: 'search'
    })

    const options = {
      enterTo: 'default-element',
      defaultElement: e.target,

      leaveFor: {
        right: '',
        left: ''
      }
    }
    updateSpatialNavigationSection({
      sectionName: MARKET_HEADER_ID,
      config: options
    })

    this.props.onNavigate(to)
  }

  render() {
    const { markets, marketFilter } = this.props

    let marketNavButtons = markets.map(city => (
      <NavButton
        className={`focusable focusable-${MARKET_HEADER_ID}`}
        onFocus={scrollNav}
        key={city.attributes.display_name}
        id={city.id}
        onClick={this.handleClick}
        isSelected={marketFilter === city.id}
      >
        {city.attributes.display_name}
      </NavButton>
    ))

    return (
      <MarketNavContainer className="nav-bar">
        <MarketNav id={MARKET_HEADER_ID}>{marketNavButtons}</MarketNav>
      </MarketNavContainer>
    )
  }
}

MarketNavHeader.propTypes = {
  markets: PropTypes.array,
  marketFilter: PropTypes.number
}

MarketNavHeader.defaultProps = {
  markets: [],
  marketFilter: ''
}

const mapStateToProps = ({ market: { marketFilter, markets } }) => {
  return { marketFilter, markets }
}
const mapDispatchToProps = dispatch => {
  return {
    requestAllMarkets: () => dispatch(marketActionCreators.requestAllMarkets()),
    onNavigate: data => dispatch(appActionCreator.onNavigate(data))
  }
}

const MarketNavHeaderWithRouter = withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(MarketNavHeader)
)
export default MarketNavHeaderWithRouter

export { MarketNavHeader }
