import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import DefaultButton from '../Shared/Buttons'
import {
  ERROR_HEADER_TEXT,
  ERROR_SUBTITLE_TEXT,
  ERROR_TRY_AGAIN_TEXT,
  EXIT_BUTTON_TEXT
} from '../../utils/string-constants'

import alert from '../../assets/ic-alert.svg'
import './error-page.scss'
import logo from '../../assets/radiocom-logo-text.svg'
import SpatialNavigationWrapper from './../Shared/SpatialNavigationWrapper'
import { focusOnDefaultElement } from './../../utils/spatial-nav'

import { NavigateDiscovery, NavigationMap } from './../../utils/navigation-map'
import $badgerReady from '../../utils/money-badger-wrapper'

export const ErrorPageContainer = styled.div`
  .error-modal-content-container {
    background-color: ${p => p.theme.colors.defaultBackground};
    padding: 2.5rem 3.625rem;
    box-sizing: border-box;
  }
`

/* istanbul ignore next */
export const ErrorPageHeader = styled.p`
  font-family: ${p => p.theme.fonts.primary};
  color: ${p => p.theme.colors.primaryLight};
  font-size: 1.5rem;
  margin-bottom: 1rem;
`
/* istanbul ignore next */
export const ErrorPageBody = styled.p`
  font-family: ${p => p.theme.fonts.book};
  color: ${p => p.theme.colors.primaryLight};
  font-size: 1.25rem;
  margin-bottom: 4rem;
`

const NavWrapper = SpatialNavigationWrapper({
  enableHorizontalScroll: false,
  enableVerticalScroll: false,
  defaultElement: 'error-page'
})

let $badger

const ErrorPage = props => {
  const [exitButtonText, setExitButtonText] = useState(EXIT_BUTTON_TEXT)
  useEffect(() => {
    props.to.parentPageKey
      ? setExitButtonText(EXIT_BUTTON_TEXT)
      : setExitButtonText(`${EXIT_BUTTON_TEXT} App`)
  }, [])

  useEffect(() => {
    async function loadBadger() {
      $badger = await $badgerReady
    }
    loadBadger()
    focusOnDefaultElement('error-page')
  }, [$badger])

  const handleTryAgain = () => {
    props.onRetry(props.retry)
    props.onNavigate(props.to)
  }

  const handleExit = () => {
    props.onCancel()
    const navObject = NavigationMap.get(props.to.parentPageKey)
    props.to.parentPageKey ? props.onNavigate(navObject) : $badger.shutdown()
  }

  return (
    <ErrorPageContainer className="error-modal-content-container">
      <img alt="error-logo" className="error-logo-image" src={logo} />
      <div className="error-page-container">
        <img alt="alert-icon" className="alert-icon" src={alert} />
        <div className="error-content-wrapper">
          <ErrorPageHeader>{ERROR_HEADER_TEXT}</ErrorPageHeader>
          <ErrorPageBody>{ERROR_SUBTITLE_TEXT}</ErrorPageBody>
          <DefaultButton
            onClick={handleTryAgain}
            className={`focusable-${props.navId} focusable`}
          >
            {ERROR_TRY_AGAIN_TEXT}
          </DefaultButton>
          <DefaultButton
            onClick={handleExit}
            className={`focusable-${props.navId} focusable`}
          >
            {exitButtonText}
          </DefaultButton>
        </div>
      </div>
    </ErrorPageContainer>
  )
}

const ErrorPageWithNav = props => (
  <NavWrapper>
    <ErrorPage navId="error-page" {...props} />
  </NavWrapper>
)

export { ErrorPage }
export default ErrorPageWithNav
