import {
  NAVIGATE_TO_PAGE,
  SHOW_LOADING,
  HIDE_LOADING,
  HIDE_SPLASH_SCREEN,
  TOGGLE_PAGE_LABEL,
  SHOW_PLAYER
} from '../utils/constants'

export const navigateToPage = data => ({ type: NAVIGATE_TO_PAGE, data })
export const showLoading = () => ({ type: SHOW_LOADING })
export const hideLoading = () => ({ type: HIDE_LOADING })
export const hideSplashScreen = () => ({ type: HIDE_SPLASH_SCREEN })
export const togglePageLabel = data => ({ type: TOGGLE_PAGE_LABEL, data })
export const showPlayer = () => ({ type: SHOW_PLAYER })
