import React, { Component, Fragment } from 'react'
import { Switch, Route } from 'react-router-dom'
import { ConnectedRouter } from 'connected-react-router'
import { bindActionCreators } from 'redux'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import 'radio-web-player/src/radio-player'
import { togglePlayer, playSelectedStation } from './utils/player'

import {
  initializeSpatialNav,
  focusEnteredComponent,
  blurEnteredComponent,
  focusOnDefaultElement,
  updateSpatialNavigationSection,
  SpatialNavigation
} from './utils/spatial-nav'

import {
  EXIT_KEY,
  BACK_KEY,
  TOGGLE_KEY,
  TOGGLE_KEY_TEST,
  JUMBO_CATEGORY_ID,
  PAGE_UP_KEY,
  PAGE_DN_KEY
} from './utils/constants'
import { NavigationMap } from './utils/navigation-map'

import ExitModal from './components/ExitModal'
import Page from './components/Page'
import { NavHeader } from './components/NavHeader'
import DiscoverPage from './components/DiscoverPage/DiscoverPage'
import CategoryPage from './components/CategoryPage'
import GenresPage from './components/GenresPage'
import SearchPage from './components/SearchPage/SearchPage'
import SettingsPage from './components/SettingsPage'
import ErrorPage from './components/ErrorPage/ErrorPage'
import SplashScreenModal from './components/SplashScreen'
import RadioPlayer from './components/RadioPlayer'
import * as appActionCreator from './asyncActionCreators/app'
import { showPlayer } from './actions/app'
import * as stationActionCreator from './asyncActionCreators/stations'
import * as errorActionCreator from './asyncActionCreators/errors'

import './App.scss'

class AppComponent extends Component {
  constructor(props) {
    super(props)

    this.state = {
      showExitModal: false,
      activeElement: null
    }
  }

  startTogglePlayer = () => {
    try {
      togglePlayer()
    } catch (e) {
      playSelectedStation()
    }
  }

  handleKeyDown = e => {
    switch (e.keyCode) {
      case BACK_KEY:
        // push parent page url
        if (this.props.parentPageKey) {
          const to = NavigationMap.get(this.props.parentPageKey)
          this.props.onNavigate(to)
        } else {
          const activeElement = document.activeElement.id

          !this.state.showExitModal &&
            this.setState(state => ({
              activeElement,
              showExitModal: true
            }))
        }
        break
      case EXIT_KEY:
        break
      case TOGGLE_KEY:
        this.startTogglePlayer()
        this.props.showPlayer()
        break
      case TOGGLE_KEY_TEST:
        this.startTogglePlayer()
        this.props.showPlayer()
        break
      case PAGE_UP_KEY:
        e.preventDefault()
        break
      case PAGE_DN_KEY:
        e.preventDefault()
        break
      default:
        break
    }
  }

  componentDidMount() {
    const { hideSplashScreen, requestModulesList } = this.props

    requestModulesList()
    hideSplashScreen(this.setInitialFocus)
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.state.showExitModal !== prevState.showExitModal) {
      const activeElement = document.getElementById(this.state.activeElement)
      SpatialNavigation.focus(activeElement)
    }
  }

  setInitialFocus = () => {
    initializeSpatialNav()
    focusOnDefaultElement(JUMBO_CATEGORY_ID)
  }

  closeModal = e => {
    this.setState(state => {
      return { showExitModal: false }
    })
  }

  render() {
    const {
      history,
      onNavigate,
      pathURL,
      currentPage,
      parentPage,
      parentPageKey,
      onRetry,
      onCancel,
      retryFn,
      retryParams,
      showSplashScreen
    } = this.props
    return (
      <ConnectedRouter history={history}>
        <Fragment>
          <SplashScreenModal open={showSplashScreen} />
          <Page
            showSplashScreen={showSplashScreen}
            id="page-container"
            onKeyDown={this.handleKeyDown}
          >
            <ExitModal
              id="exit-modal"
              closeModal={this.closeModal}
              blurComponent={blurEnteredComponent}
              focusComponent={focusEnteredComponent}
              open={this.state.showExitModal}
            />
            <NavHeader
              onNavigate={onNavigate}
              pathURL={pathURL}
              currentPage={currentPage}
              parentPage={parentPage}
            />
            <Switch>
              <Route exact path="/" component={DiscoverPage} {...this.props} />
              <Route
                path="/search"
                render={props => <SearchPage {...props} />}
              />
              <Route path="/settings" component={SettingsPage} />
              <Route
                path="/category/:categoryId"
                component={CategoryPage}
                {...this.props}
              />
              <Route
                path="/genre/:genreId"
                component={CategoryPage}
                {...this.props}
              />
              <Route path="/music" component={GenresPage} {...this.props} />
              <Route
                path="/error"
                render={() => (
                  <ErrorPage
                    onNavigate={onNavigate}
                    to={{ pathURL, currentPage, parentPage, parentPageKey }}
                    onRetry={onRetry}
                    onCancel={onCancel}
                    retry={{ retryFn, retryParams }}
                  />
                )}
              />
            </Switch>
          </Page>
        </Fragment>
      </ConnectedRouter>
    )
  }
}

AppComponent.propTypes = {
  showSplashScreen: PropTypes.bool
}

AppComponent.defaultProps = {
  showSplashScreen: true,
  modules: []
}

const mapStateToProps = ({
  app: {
    showSplashScreen,
    pathURL,
    currentPage,
    parentPage,
    parentPageKey
  },
  stations: { modules },
  error: { retryFn, retryParams }
}) => {
  return {
    showSplashScreen,
    modules,
    pathURL,
    currentPage,
    parentPage,
    parentPageKey,
    retryFn: retryFn,
    retryParams: retryParams
  }
}

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
    {
      hideSplashScreen: appActionCreator.hideSplashScreen,
      onNavigate: appActionCreator.onNavigate,
      requestModulesList: stationActionCreator.requestModulesList,
      onRetry: errorActionCreator.onRetry,
      onCancel: errorActionCreator.onCancel,
      showPlayer
    },
    dispatch
  )
}

const App = connect(
  mapStateToProps,
  mapDispatchToProps
)(AppComponent)

export default App
