import React from 'react'
import styled, { ThemeProvider } from 'styled-components'

import theme from '../utils/theme'

const PageContainer = styled.div`
  padding-top: 9rem;
  padding-left: 5.25rem;
  position: relative;
  min-height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
  box-sizing: border-box;
  opacity: ${props => (props.showSplashScreen ? 0 : 1)};
`
const PageComponent = props => {
  const { children, onKeyDown, showSplashScreen } = props
  return (
    <ThemeProvider theme={theme}>
      <PageContainer onKeyDown={onKeyDown} showSplashScreen={showSplashScreen}>
        {children}
      </PageContainer>
    </ThemeProvider>
  )
}

export default PageComponent
