import axios from 'axios';
import Logger from '../utils/logger';
import { RADIO_API_SERVER, PROVISION_URL } from '../constants/main';

var getData = function getData(response) {
  return response.data;
};

var logError = function logError(error) {
  Logger.error(error); // Ensure the error is not siltenly captured when loggin.

  throw error;
};

var getFallbackData = function getFallbackData() {
  return {
    data: []
  };
};

var getFallbackSchedule = getFallbackData;
var getFallbackSpecialEvents = getFallbackData;
var getFallbackSegments = getFallbackData;

var getStation = function getStation(stationId) {
  return axios.get("".concat(RADIO_API_SERVER, "/v1/stations/").concat(stationId)).then(function (response) {
    return response.data.data;
  }).catch(logError);
};

var getSchedule = function getSchedule(stationId) {
  return axios.get("".concat(RADIO_API_SERVER, "/v1/schedules?sort=day_of_week,start_time&filter[station_id]=").concat(stationId, "&page[size]=100&page[number]=1")).then(getData).catch(logError).catch(getFallbackSchedule);
};

var getSpecialEvents = function getSpecialEvents(stationId) {
  return axios.get("".concat(RADIO_API_SERVER, "/v1/schedules/specials?filter[station_id]=").concat(stationId, "&page[size]=100&page[number]=1")).then(getData).catch(logError).catch(getFallbackSpecialEvents);
};

var getSegments = function getSegments(_ref) {
  var stationId = _ref.stationId,
      start_time = _ref.start_time,
      end_time = _ref.end_time;
  return axios.get("".concat(RADIO_API_SERVER, "/v1/stations/").concat(stationId, "/segments?filter[start_time]=[").concat(start_time, ",").concat(end_time, "]&page[size]=1000")).then(getData).catch(logError).catch(getFallbackSegments);
};

var getMounts = function getMounts(stationName) {
  return axios.get("".concat(PROVISION_URL).concat(stationName)).then(getData).catch(logError);
};

var getCompanionAd = function getCompanionAd(url) {
  return axios.get(url).then(getData).catch(logError);
};

export default {
  getStation: getStation,
  getSchedule: getSchedule,
  getSegments: getSegments,
  getMounts: getMounts,
  getCompanionAd: getCompanionAd,
  getSpecialEvents: getSpecialEvents
};