import {
  NAVIGATE_TO_PAGE,
  SHOW_LOADING,
  HIDE_LOADING,
  HIDE_SPLASH_SCREEN,
  TOGGLE_PAGE_LABEL,
  SHOW_PLAYER
} from '../utils/constants'

export default (
  state = Object.assign({
    showSplashScreen: true,
    pathURL: '/',
    showPageLabel: false,
    currentPage: 'Discover',
    parentPage: null,
    parentPageKey: null,
    isPlayerVisible: false
  }),
  action
) => {
  switch (action.type) {
    case NAVIGATE_TO_PAGE:
      return Object.assign({}, state, {
        pathURL: action.data.pathURL,
        currentPage: action.data.currentPage,
        parentPage: action.data.parentPage,
        parentPageKey: action.data.parentPageKey
      })
    case SHOW_LOADING:
      return Object.assign({}, state, {
        isLoading: true
      })
    case HIDE_LOADING:
      return Object.assign({}, state, {
        isLoading: false
      })
    case HIDE_SPLASH_SCREEN:
      return Object.assign({}, state, {
        showSplashScreen: false
      })
    case TOGGLE_PAGE_LABEL:
      return Object.assign({}, state, {
        showPageLabel: !action.data
      })
    case SHOW_PLAYER:
      return Object.assign({}, state, {
        isPlayerVisible: true
      })
    default:
      return state
  }
}
