import {
  GET_MODULES_BY_ID,
  GET_MODULES_BY_ID_SUCCESS,
  GET_MODULES_BY_ID_FAILURE,
  GET_MODULES_LIST,
  GET_MODULES_LIST_SUCCESS,
  GET_MODULES_LIST_FAILED,
  GET_GENRES_BY_ID,
  CLEAR_CATEGORY_STATIONS,
  GET_SEARCH_RESULTS,
  GET_GENRES_BY_SLUGS,
  SET_DISCOVERY_CAROUSEL
} from '../utils/constants'

export const getModulesList = () => ({
  type: GET_MODULES_LIST
})

export const getModulesListSuccess = data => ({
  type: GET_MODULES_LIST_SUCCESS,
  data
})

export const getModulesListFailed = data => ({
  type: GET_MODULES_LIST_FAILED,
  data
})

export const getModulesByID = () => ({
  type: GET_MODULES_BY_ID
})

export const getModulesByIDSuccess = retrievedStations => ({
  type: GET_MODULES_BY_ID_SUCCESS,
  data: retrievedStations
})

export const getModulesByIDFailure = error => ({
  type: GET_MODULES_BY_ID_FAILURE,
  data: error
})

export const getGenresById = data => ({
  type: GET_GENRES_BY_ID,
  data
})

export const clearCategoryStations = () => ({
  type: CLEAR_CATEGORY_STATIONS
})

export const getGenresBySlugs = data => ({
  type: GET_GENRES_BY_SLUGS,
  data
})

export const setDiscoveryCarousel = data => ({
  type: SET_DISCOVERY_CAROUSEL,
  data
})

export const getSearchResults = data => ({
  type: GET_SEARCH_RESULTS,
  data
})
