import 'radio-web-player/src/radio-player'
import {
  RADIO_PLAYER_BASE_URL,
  PLAYER_CONTAINER_ID,
  DEFAULT_PLAYER_ID
} from './../utils/constants'
import { updateSpatialNavigationSection } from '../utils/spatial-nav'
export const RadioPlayer = window.RadioPlayer

export const playSelectedStation = (id = DEFAULT_PLAYER_ID) => {
  RadioPlayer.initialize({
    bottomPlayer: false,
    overlay: true,
    baseUrl: RADIO_PLAYER_BASE_URL
  })


  RadioPlayer.updateStationList([id])

}

export const pausePlayer = () => {
  RadioPlayer.pause()
}

export const resumePlayer = () => {
  RadioPlayer.play()
}

export const togglePlayer = () => RadioPlayer.toggle()
