export const MODULES_CAROUSEL_TYPE = {
  local_station_carousel: 'CAROUSEL_STATION_COMPONENT',
  recent_station_carousel: 'CAROUSEL_STATION_COMPONENT',
  category_3x3: 'CAROUSEL_GENRE_COMPONENT',
  featured_carousel: 'CAROUSEL_FEATURED_COMPONENT',
  station_list: 'CAROUSEL_STATION_COMPONENT'
}

export const navIdForModule = module => {
  let returnVal = {}

  if (!module.attributes) {
    const regExMatch = new RegExp('[^a-zA-Z0-9]')
    const formatModuleName = module.name.replace(regExMatch, '_')
    returnVal = { navId: `${formatModuleName}_${module.id}` }
  } else {
    returnVal = { navId: `${module.attributes.type}_${module.id}` }
  }
  return returnVal
}

export const injectSpatialNavId = moduleArray => {
  return moduleArray.map(module => {
    const navId = navIdForModule(module)
    return { ...module, ...navId }
  })
}
export const MODULES_BLACKLISTED = new Set([
  'favorite_stations',
  'podcast_carousel',
  'podcast_carousel',
  'feature_page',
  'featured_carousel'
])

export const isModuleValid = module => {
  return (
    !MODULES_BLACKLISTED.has(module.attributes.type) &&
    !module.attributes.name.toLowerCase().includes('podcast') &&
    !module.attributes.type.toLowerCase().includes('podcast')
  )
}

export const propsForStationOfType = ({ station, type = 'square' }) => {
  switch (MODULES_CAROUSEL_TYPE[type]) {
    case 'CAROUSEL_STATION_COMPONENT':
      return {
        cardType: 'square',
        cardLabel: station.attributes.name,
        imgUrl: station.attributes.square_logo_small
      }
    case 'CAROUSEL_GENRE_COMPONENT':
      return {
        cardType: 'circle',
        cardLabel: station.attributes.title,
        imgUrl: station.attributes.image
      }
    case 'CAROUSEL_FEATURED_COMPONENT':
      return {
        cardType: 'banner',
        cardLabel: station.attributes.title,
        imgUrl: station.attributes.square_logo_small
      }
    default:
      return {
        cardType: 'square',
        cardLabel: station.attributes.name,
        imgUrl: station.attributes.square_logo_small
      }
  }
}

export const formatGenreStations = stations => {
  return stations.reduce((acc, station) => {
    station.attributes.genre_name.forEach(genre => {
      genre = genre.replace(/(\s|\/)/g, '-')
      acc[genre] = acc[genre] ? [...acc[genre], station] : [station]
    })
    return acc
  }, {})
}

