import {
  REQUEST_ALL_MARKETS_SUCCESS,
  REQUEST_ALL_MARKETS_FAILED,
  FILTER_STATIONS_BY_MARKETS,
  GET_MARKET_BY_ID,
  GET_MARKET_BY_ID_SUCCESS,
  GET_MARKET_BY_ID_FAILED
} from '../utils/constants'

export const requestAllMarketsSuccess = data => ({
  type: REQUEST_ALL_MARKETS_SUCCESS,
  data
})
export const requestAllMarketsFailed = data => ({
  type: REQUEST_ALL_MARKETS_FAILED,
  data
})

export const filterStationsByMarkets = data => ({
  type: FILTER_STATIONS_BY_MARKETS,
  data
})

export const getStationsByMarketId = () => ({ type: GET_MARKET_BY_ID })

export const getStationsByMarketIdSuccess = data => ({
  type: GET_MARKET_BY_ID_SUCCESS,
  data
})
export const getStationsByMarketIdFailed = data => ({
  type: GET_MARKET_BY_ID_FAILED,
  data
})
