import * as market from '../actions/market'
import * as api from '../utils/api'
import * as errors from '../asyncActionCreators/errors'

export const requestAllMarkets = () => dispatch => {
  api
    .getMarkets()
    .then(res => {
      dispatch(market.requestAllMarketsSuccess(res.data.data))
    })
    .catch(err => {
      dispatch(market.requestAllMarketsFailed(err))
      dispatch(errors.onError(requestAllMarkets))
    })
}

export const requestStationsByMarketId = ({ marketId }) => dispatch => {
  dispatch(market.getStationsByMarketId())

  api
    .requestStationsByMarketId({ marketId })
    .then(res => {
      dispatch(market.getStationsByMarketIdSuccess(res.data.data))
    })
    .catch(err => {
      dispatch(market.getStationsByMarketIdFailed(err))
      dispatch(errors.onError(requestStationsByMarketId, marketId))
    })
}
