import {
  GET_MUSIC_STATIONS_BY_ID,
  GET_MUSIC_STATIONS_BY_ID_SUCCESS,
  GET_MUSIC_STATIONS_BY_ID_FAILED,
  SAVE_MUSIC_GENRE_LIST,
  FILTER_STATIONS_BY_GENRES,
  RESET_FILTER_STATIONS_BY_GENRES
} from '../utils/constants'

export default (
  state = Object.assign({ isLoading: true, genreFilter: 'All' }),
  action
) => {
  switch (action.type) {
    case GET_MUSIC_STATIONS_BY_ID:
      return Object.assign({}, state, {
        isLoading: true
      })
    case GET_MUSIC_STATIONS_BY_ID_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        musicGenre: action.data
      })
    case GET_MUSIC_STATIONS_BY_ID_FAILED:
      return Object.assign({}, state, {
        isLoading: false
      })
    case SAVE_MUSIC_GENRE_LIST:
      return Object.assign({}, state, {
        genreCategories: action.data
      })
    case FILTER_STATIONS_BY_GENRES:
      return Object.assign({}, state, {
        genreFilter: action.data
      })
    case RESET_FILTER_STATIONS_BY_GENRES:
      return Object.assign({}, state, {
        genreFilter: 'All'
      })
    default:
      return state
  }
}
