import '../vendor/money-badger.min'

// As the event might happen before listening to it, a fallback timeout will check
// for the presence of the property in window and return it if present.
const waitForEventWithTimeout = ({
  emitter = document,
  eventName,
  timeout = 500,
  globalName
}) =>
  new Promise((resolve, reject) => {
    const timer = setTimeout(() => {
      emitter.removeEventListener(eventName, listener)

      window[globalName]
        ? resolve({ [globalName]: window[globalName], timeout: true })
        : reject(`Error: ${globalName} wasn't loaded in time.`)
    }, timeout)

    const listener = () => {
      clearTimeout(timer)
      emitter.removeEventListener(eventName, listener)
      resolve({ [globalName]: window[globalName], timeout: false })
    }

    emitter.addEventListener(eventName, listener)
  })

const get$Badger = ({ $badger }) => $badger

// Execute `waitForEventWithTimeout` so the event it's listened A$AP.
const $badgerReady = waitForEventWithTimeout({
  eventName: 'onMoneyBadgerReady',
  timeout: 1000,
  globalName: '$badger'
}).then(get$Badger)

export default $badgerReady
