import React, { useEffect } from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import NavigationMap from '../../factories/navigationFactory'
import { scrollToSection } from '../../utils/spatial-nav'

import * as stationActionCreator from '../../asyncActionCreators/stations'

import {
  NEWS_CATEGORY_LABEL,
  SPORTS_CATEGORY_LABEL,
  MUSIC_CATEGORY_LABEL
} from '../../utils/string-constants'

import { NavigateCategoryMusic } from '../../utils/navigation-map'

import theme from '../../utils/theme'

import MUSIC_URL from '../../assets/music@3x.png'
import SPORTS_URL from '../../assets/sports@3x.png'
import NEWS_URL from '../../assets/news-talk@3x.png'

const JumboCategory = styled.div`
  margin-bottom: 6.25rem;
  margin-top: 1.5rem;
`
/* istanbul ignore next */
export const CategoryButton = styled.button`
  width: 22.5rem;
  height: 15rem;
  border: none;
  border-top: 0.375rem solid;
  transition: transform 0.2s ease-in-out;
  box-sizing: border-box;
  padding: 0;
  color: ${p => p.theme.colors.primaryLight};
  background: #000000 url(${p => p.imageUrl}) no-repeat center/100%;
  border-top-color: ${p => p.color}
  font-size: 1.25rem;
  font-family: ${p => p.theme.fonts.black}
  &:not(:last-of-type) {
    margin-right: 1rem;
  }
  
  &:focus {
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.8);
    transition-delay: 0.11s;
    transform: scale(1.14);
    outline: none;
  }
`

const CategorySectionButtons = props => {
  const { getGenreIdsBySlugs, newsTalk, sports } = props
  let navigateNews = {}
  let navigateSports = {}
  useEffect(() => {
    if (!(newsTalk || sports)) {
      getGenreIdsBySlugs({ slugs: ['news-talk', 'sports', 'music'] })
    } else {
      createNavigationMap({ newsTalk, sports })
    }
  }, [newsTalk, sports])
  const createNavigationMap = ({ newsTalk, sports }) => {
    navigateNews = new NavigationMap({
      pathURL: `/category/${newsTalk.id}`,
      parentPage: 'discover',
      currentPage: newsTalk.attributes.name,
      parentPageKey: 'discover'
    })
    navigateSports = new NavigationMap({
      pathURL: `/category/${sports.id}`,
      parentPage: 'discover',
      currentPage: sports.attributes.name,
      parentPageKey: 'discover'
    })
  }

  return (
    <JumboCategory id={props.module.navId}>
      <CategoryButton
        id={NEWS_CATEGORY_LABEL}
        color={theme.colors.accentYellow}
        className={`focusable-${props.module.navId} focusable`}
        imageUrl={NEWS_URL}
        onFocus={scrollToSection}
        onClick={() => props.onNavigate(navigateNews)}
      >
        {NEWS_CATEGORY_LABEL}
      </CategoryButton>
      <CategoryButton
        id={SPORTS_CATEGORY_LABEL}
        color={theme.colors.accentBlue}
        className={`focusable-${props.module.navId} focusable`}
        imageUrl={SPORTS_URL}
        onFocus={scrollToSection}
        onClick={() => props.onNavigate(navigateSports)}
      >
        {SPORTS_CATEGORY_LABEL}
      </CategoryButton>
      <CategoryButton
        id={MUSIC_CATEGORY_LABEL}
        color={theme.colors.accentRed}
        className={`focusable-${props.module.navId} focusable`}
        imageUrl={MUSIC_URL}
        onFocus={scrollToSection}
        onClick={() => props.onNavigate(NavigateCategoryMusic)}
      >
        {MUSIC_CATEGORY_LABEL}
      </CategoryButton>
    </JumboCategory>
  )
}

CategorySectionButtons.propTypes = {
  getGenreIdsBySlug: PropTypes.func,
  newsTalkId: PropTypes.number,
  sportsId: PropTypes.number
}

const mapStateToProps = ({ stations: { 'news-talk': newsTalk, sports } }) => {
  return {
    newsTalk,
    sports
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getGenreIdsBySlugs: data =>
      dispatch(stationActionCreator.getGenreIdsBySlugs(data))
  }
}
const CategorySection = connect(
  mapStateToProps,
  mapDispatchToProps
)(CategorySectionButtons)
export { CategorySectionButtons }
export default CategorySection
