import * as music from '../actions/music'
import * as api from '../utils/api'
import * as errors from '../asyncActionCreators/errors'
import { genreItems, formatGenreStations } from '../utils/modules'

export const requestAllGenreModules = () => dispatch => {
  dispatch(music.getMusicStationsByGenreId())
  api
    .getStationsByGenreId(genreItems[0].id)
    .then(res => {
      const formattedGenres = formatGenreStations(res.data.data)
      dispatch(music.getMusicStationsByGenreIdSuccess(formattedGenres))
    })
    .catch(err => {
      dispatch(music.getMusicStationsByGenreIdFailed(err))
      dispatch(errors.onError(requestAllGenreModules))
    })
}
