import React from 'react'
import styled from 'styled-components'
import TrackVisibility from 'react-on-screen'

import { scrollToCard } from '../../../utils/spatial-nav'
import StationCard from '../Cards'
import { FixedSizeList as List } from 'react-window'
import { propsForStationOfType } from '../../../utils/modules'
import {
  REACT_WINDOW_VIEWPORT_WIDTH,
  CAROUSEL_HEIGHT,
  CAROUSEL_ITEM_SIZE,
  OVERSCAN_COUNT
} from '../../../utils/constants'
import './carousel.scss'

const CarouselContainer = styled.div`
  opacity: 0.6;
  position: relative;

  &:focus-within {
    opacity: 1;
  }
`
const CarouselHeader = styled.div`
  color: ${p => p.theme.colors.primaryLight};
  font-family: ${p => p.theme.fonts.primary};
  font-size: 1.5rem;
  height: 5.25rem;
  display: flex;
  align-items: center;
`

const StationCarousel = props => {
  return (
    <div className="carousel-container">
      <CarouselHeader className="carousel-header">
        {props.headerText}
      </CarouselHeader>
      {!props.isDiscoverPageCarousel ? (
        <CarouselWithTrackVisibility>
          <Carousel {...props} />
        </CarouselWithTrackVisibility>
      ) : (
        <Carousel {...props} />
      )}
    </div>
  )
}

const Carousel = ({ id, stations, type, carouselPosition, handleKeyPress }) => (
  <CarouselContainer id={id} className="carousel" data-carouselname={id}>
    <List
      layout="horizontal"
      height={CAROUSEL_HEIGHT}
      itemCount={stations.length}
      itemSize={CAROUSEL_ITEM_SIZE}
      width={REACT_WINDOW_VIEWPORT_WIDTH}
      itemData={stations}
      overscanCount={OVERSCAN_COUNT}
      className={`list-${id} virtualized`}
    >
      {({ index, style }) => {
        const station = stations[index]
        const stationProps = propsForStationOfType({
          station,
          type
        })

        return (
          <div style={style} className="column">
            <StationCard
              {...stationProps}
              cardId={station.id}
              key={`station-card-${id}-${index}`}
              scrollToCard={scrollToCard}
              focusProp={`focusable-${id}`}
              parentCarousel={id}
              cardPosition={index}
              carouselPosition={carouselPosition}
              handleKeyPress={handleKeyPress}
            />
          </div>
        )
      }}
    </List>
  </CarouselContainer>
)

const CarouselWithTrackVisibility = ({ children }) => (
  <TrackVisibility partialVisibility offset={150}>
    {({ isVisible }) => isVisible && children}
  </TrackVisibility>
)

export default StationCarousel
