import React, { useEffect } from 'react'
import styled from 'styled-components'
import {
  focusEnteredComponent,
  focusOnDefaultElement,
  blurEnteredComponent,
  updateSpatialNavigationSection,
  SpatialNavigation
} from '../../utils/spatial-nav'

import {
  ENTER_KEY,
  PLAYER_CONTAINER_ID,
  NAV_SPATIAL_NAV_ID
} from '../../utils/constants'
import { togglePlayer } from '../../utils/player'

const RadioPlayerContainer = styled.div`
  display: ${p => (p.isPlayerVisible ? 'block' : 'none')};
  &:focus-within {
    #overlayPlayerContainer {
      border: white 3px solid;
    }
  }
`
const RadioPlayer = ({ isPlayerVisible, activeElement }) => {
  useEffect(() => {
    focusEnteredComponent({
      cmpNavId: PLAYER_CONTAINER_ID,
      leftCmp: `@${NAV_SPATIAL_NAV_ID}`,
      rightCmp: undefined,
      enterToLastFocused: false
    })
    focusOnDefaultElement(activeElement)
    return () => blurEnteredComponent(PLAYER_CONTAINER_ID)
  }, [isPlayerVisible])

  const togglePlayerState = e => {
    switch (e.keyCode) {
      case ENTER_KEY:
        togglePlayer()
        break
      default:
        break
    }
  }

  return (
    <RadioPlayerContainer
      id={PLAYER_CONTAINER_ID}
      navId={PLAYER_CONTAINER_ID}
      className={`focusable-${PLAYER_CONTAINER_ID}`}
      onKeyDown={togglePlayerState}
      isPlayerVisible={isPlayerVisible}
    />
  )
}

export default RadioPlayer
