import {
  REQUEST_ALL_MARKETS_SUCCESS,
  REQUEST_ALL_MARKETS_FAILED,
  FILTER_STATIONS_BY_MARKETS,
  GET_MARKET_BY_ID,
  GET_MARKET_BY_ID_SUCCESS,
  GET_MARKET_BY_ID_FAILED
} from '../utils/constants'

export default (state = {}, action) => {
  switch (action.type) {
    case REQUEST_ALL_MARKETS_SUCCESS:
      return Object.assign({}, state, {
        markets: action.data
      })
    case REQUEST_ALL_MARKETS_FAILED:
      return Object.assign({}, state, {
        isLoading: false,
        error: true,
        errorMessage: action.data.error
      })
    case FILTER_STATIONS_BY_MARKETS:
      return Object.assign({}, state, {
        marketFilter: action.data
      })
    case GET_MARKET_BY_ID:
      return Object.assign({}, state, {
        isLoading: true
      })
    case GET_MARKET_BY_ID_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        stations: action.data,
        marketFilter: action.data[0].attributes.market_id
      })
    case GET_MARKET_BY_ID_FAILED:
      return Object.assign({}, state, {
        isLoading: false,
        error: true,
        errorMessage: action.data.error
      })
    default:
      return state
  }
}
