import { sortBy } from 'lodash'
import * as stations from '../actions/stations'
import * as errors from '../asyncActionCreators/errors'
import * as api from '../utils/api'
import { navIdForModule } from '../utils/modules'

export const requestModulesList = () => async dispatch => {
  dispatch(stations.getModulesList())

  try {
    // const categoryStations = await api.getStations()
    const modulesResponse = await api.getModulesByPage()

    const modules = modulesResponse.modules.map(module => {
      const navId = navIdForModule(module)
      return {
        ...module,
        ...navId
      }
    })
    dispatch(stations.getModulesListSuccess({ modules }))
  } catch (error) {
    dispatch(errors.onError(requestModulesList))
    dispatch(stations.getModulesListFailed(error))
  }
}

export const requestGenresById = id => dispatch => {
  const getData = res => res.data.data
  api
    .getStationsByGenreId(id)
    .then(getData)
    .then(stationsByGenreId => {
      const categoryStations = sortBy(stationsByGenreId, station => {
        return station.attributes.sort_order
      })
      dispatch(stations.getGenresById({ categoryStations }))
    })
    .catch(err => {
      dispatch(errors.onError(requestGenresById, id))
    })
}

export const getGenreIdsBySlugs = ({ slugs }) => dispatch => {
  const filters = `filter[slug]=${slugs}`
  api
    .getGenres({ filters })
    .then(({ genres }) => {
      const genreList = genres.reduce((acc, genre) => {
        acc[genre.attributes.slug] = genre
        return acc
      }, {})
      dispatch(stations.getGenresBySlugs(genreList))
    })
    .catch(err => {
      dispatch(errors.onError(getGenreIdsBySlugs, { slugs }))
    })
}
export const requestDiscoveryStations = () => dispatch => {
  api.getStations().then(res => {
    dispatch(stations.setDiscoveryCarousel(res))
  })
}

export const requestStationsBySearchTerm = query => dispatch => {
  if (query === 'er')
    dispatch(errors.onError(requestStationsBySearchTerm, query))
  api
    .getStationsBySearchTerm(query)
    .then(res => {
      dispatch(stations.getSearchResults(res))
    })
    .catch(err => {
      dispatch(errors.onError(requestStationsBySearchTerm, query))
    })
}
