<p style="text-align: justify;"><b>Protection of Information.&nbsp;&nbsp;</b>We are committed to protecting your information.&nbsp;&nbsp;We have adopted commercially reasonable technical, administrative, and physical procedures to help protect your information from loss, misuse, unauthorized access&nbsp;and&nbsp;alteration.&nbsp;&nbsp;Please note that no data transmission or storage can be guaranteed to be 100% secure.&nbsp;&nbsp;We want you to feel confident using Entercom Services, but We cannot ensure or warrant the security of any information you transmit to Us.&nbsp;&nbsp;Please refer to the&nbsp;U.S. Federal Trade Commission’s website for information on how to protect yourself from identity theft.</p>
