import {
  GET_MODULES_BY_ID,
  GET_MODULES_BY_ID_SUCCESS,
  GET_MODULES_BY_ID_FAILURE,
  GET_MODULES_LIST,
  GET_MODULES_LIST_SUCCESS,
  GET_MODULES_LIST_FAILED,
  GET_GENRES_BY_ID,
  CLEAR_CATEGORY_STATIONS,
  GET_GENRES_BY_SLUGS,
  GET_SEARCH_RESULTS,
  SET_DISCOVERY_CAROUSEL
} from '../utils/constants'

export default (
  state = {
    isLoading: true,
    searchStations: []
  },
  action
) => {
  switch (action.type) {
    case GET_MODULES_BY_ID:
      return Object.assign({}, state, {
        isLoading: true
      })
    case GET_MODULES_BY_ID_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        stations: action.data
      })
    case GET_MODULES_BY_ID_FAILURE:
      return Object.assign({}, state, {
        isLoading: false,
        error: true,
        errorMessage: action.data.error
      })
    case GET_MODULES_LIST:
      return Object.assign({}, state, {
        isLoading: true,
        modules: action.data
      })
    case GET_MODULES_LIST_SUCCESS:
      return Object.assign({}, state, {
        isLoading: false,
        ...action.data
      })
    case GET_MODULES_LIST_FAILED:
      return Object.assign({}, state, {
        isLoading: false,
        ...action.data
      })
    case GET_GENRES_BY_ID:
      return Object.assign({}, state, {
        ...action.data
      })
    case CLEAR_CATEGORY_STATIONS:
      return Object.assign({}, state, {
        categoryStations: []
      })
    case GET_GENRES_BY_SLUGS:
      return Object.assign({}, state, {
        ...action.data
      })
    case GET_SEARCH_RESULTS:
      return Object.assign({}, state, {
        ...action.data
      })

    case SET_DISCOVERY_CAROUSEL:
      return Object.assign({}, state, ...action.data)

    default:
      return state
  }
}
