import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { findIndex } from 'lodash'
import styled from 'styled-components'

import SpatialNavigationWrapper from '../Shared/SpatialNavigationWrapper'
import LoadingSpinner from '../Shared/LoadingSpinner'

import {
  updateSpatialNavigationSection,
  focusOnDefaultElement
} from '../../utils/spatial-nav'
import { jumboCategoryModule } from '../../utils/modules'
import { modulesFactory } from '../../factories/moduleFactory'
import {
  NAV_SPATIAL_NAV_ID,
  JUMBO_CATEGORY_ID,
  LOCAL_STATIONS_CAROUSEL_ID,
  GENRES_CAROUSEL_ID,
  NEWS_CAROUSEL_ID
} from '../../utils/constants'
import * as appAction from '../../asyncActionCreators/app'

const DiscoverContainer = styled.div`
  &:last-child {
    margin-bottom: 50vh;
  }
`
const DiscoverPageComponent = props => {
  const { modules, isLoading, onNavigate } = props

  // Include Jumbo category into the list of modules
  if (findIndex(modules, jumboCategoryModule) === -1)
    modules.unshift(jumboCategoryModule)

  useEffect(() => {
    focusOnDefaultElement(JUMBO_CATEGORY_ID)
  }, [isLoading])
  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: 'auto' })
  }, [])
  // Create Module Components
  const ModuleComponents = modules
    .filter(
      module =>
        (module.attributes.destinations &&
          module.attributes.destinations.length > 0) ||
        module.attributes.special
    )
    .map(module => {
      const ModComponent = modulesFactory(module, props)
      return (
        <ModComponent
          navId={module.navId}
          key={`${module.id}_${module.name}`}
          onNavigate={onNavigate}
        />
      )
    })

  return (
    <DiscoverContainer>
      {isLoading ? (
        <LoadingSpinner />
      ) : (
        <DiscoverPageContentWithNav>
          {ModuleComponents}
        </DiscoverPageContentWithNav>
      )}
    </DiscoverContainer>
  )
}

const DiscoverPageContentWithNav = SpatialNavigationWrapper({
  previousComponentMap: {
    [JUMBO_CATEGORY_ID]: NAV_SPATIAL_NAV_ID,
    [LOCAL_STATIONS_CAROUSEL_ID]: JUMBO_CATEGORY_ID,
    [GENRES_CAROUSEL_ID]: LOCAL_STATIONS_CAROUSEL_ID,
    [NEWS_CAROUSEL_ID]: GENRES_CAROUSEL_ID
  },
  nextComponentMap: {
    [JUMBO_CATEGORY_ID]: LOCAL_STATIONS_CAROUSEL_ID,
    [LOCAL_STATIONS_CAROUSEL_ID]: GENRES_CAROUSEL_ID,
    [GENRES_CAROUSEL_ID]: NEWS_CAROUSEL_ID
  }
})

DiscoverPageComponent.propTypes = {
  isLoading: PropTypes.bool
}

DiscoverPageComponent.defaultProps = {
  isLoading: true,
  modules: []
}

const mapStateToProps = ({ stations: { isLoading, modules } }) => {
  return {
    isLoading,
    modules
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onNavigate: data => dispatch(appAction.onNavigate(data))
  }
}

const DiscoverPage = connect(
  mapStateToProps,
  mapDispatchToProps
)(DiscoverPageComponent)

export { DiscoverPageComponent }
export default DiscoverPage
