import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'

import { focusOnDefaultElement } from '../../utils/spatial-nav'
import { CATEGORY_NAV_ID } from '../../utils/constants'

import * as appActionCreator from '../../asyncActionCreators/app'
import * as stationActionCreator from '../../asyncActionCreators/stations'
import * as stationActions from '../../actions/stations'

import SpatialNavigationWrapper from '../Shared/SpatialNavigationWrapper'
import GridView from '../Shared/GridView'
import LoadingSpinner from '../Shared/LoadingSpinner'

const CategoryPageComponent = ({
  requestGenresById,
  categoryStations,
  clearCategoryStations,
  match: {
    params: { categoryId }
  }
}) => {
  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: 'auto' })
    requestGenresById(categoryId)

    return () => {
      clearCategoryStations()
    }
  }, [categoryId])

  useEffect(() => {
    categoryStations && focusOnDefaultElement(CATEGORY_NAV_ID)
  }, [categoryStations])

  return !categoryStations ? (
    <LoadingSpinner />
  ) : (
    <div id="category-page-container">
      <CategoryPageWithNav>
        <GridView navId={CATEGORY_NAV_ID} stations={categoryStations} />
      </CategoryPageWithNav>
    </div>
  )
}

const CategoryPageWithNav = SpatialNavigationWrapper({
  defaultElement: `focusable-${CATEGORY_NAV_ID}`
})

CategoryPageComponent.propTypes = {
  categoryStations: PropTypes.array,
  requestGenresById: PropTypes.func
}

const mapStateToProps = ({ stations: { categoryStations } }) => {
  return {
    categoryStations
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onNavigate: data => dispatch(appActionCreator.onNavigate(data)),
    requestGenresById: data =>
      dispatch(stationActionCreator.requestGenresById(data)),
    clearCategoryStations: () =>
      dispatch(stationActions.clearCategoryStations())
  }
}

const CategoryPage = connect(
  mapStateToProps,
  mapDispatchToProps
)(CategoryPageComponent)

export { CategoryPageComponent }
export default CategoryPage
