import React from 'react'
import { FixedSizeGrid as Grid } from 'react-window'

import StationCard from '../Cards'

import { scrollGridView } from '../../../utils/spatial-nav'
import { propsForStationOfType } from '../../../utils/modules'

import {
  REACT_WINDOW_VIEWPORT_WIDTH,
  REACT_WINDOW_VIEWPORT_HEIGHT,
  CAROUSEL_ITEM_SIZE,
  GRIDVIEW_ROW_HEIGHT,
  GRIDVIEW_ITEMS_PER_ROW
} from '../../../utils/constants'

import './gridview.scss'

const GridViewComponent = ({ stations = [], navId }) => {
  const Cell = ({ rowIndex, columnIndex, style, data }) => {
    const index = rowIndex * GRIDVIEW_ITEMS_PER_ROW + columnIndex
    const station = data[index]

    // react-window wil try and render the full column count even if there's no data, so return null
    if (!station) return null

    const stationProps = propsForStationOfType({ station: station })

    return (
      <div style={style}>
        <StationCard
          {...stationProps}
          cardId={station.id}
          key={`station-card-${station.id}`}
          scrollToCard={scrollGridView}
          focusProp={`focusable-${navId}`}
          cardPosition={rowIndex}
        />
      </div>
    )
  }

  return (
    <div id="grid-view" className="grid-view-container">
      <Grid
        height={REACT_WINDOW_VIEWPORT_HEIGHT}
        width={REACT_WINDOW_VIEWPORT_WIDTH}
        columnCount={GRIDVIEW_ITEMS_PER_ROW}
        columnWidth={CAROUSEL_ITEM_SIZE}
        rowCount={Math.ceil(stations.length / GRIDVIEW_ITEMS_PER_ROW)}
        rowHeight={GRIDVIEW_ROW_HEIGHT}
        itemData={stations}
        className="virtual-grid"
      >
        {Cell}
      </Grid>
    </div>
  )
}

export default GridViewComponent
