import {
    // GET_ALL_ERRORS,
    // ADD_ERROR,
    // REMOVE_ERROR,
    DISPLAY_ERROR,
    HIDE_ERROR,
    RETRY_PREVIOUS_ACTION
} from "../utils/constants"

export default (state = {
    error: false
}, action) => {
    switch (action.type) {
        // case GET_ALL_ERRORS:
        //     return Object.assign({}, state)
        // case ADD_ERROR:
        //     return Object.assign({}, state.concat([action.error]))
        // case REMOVE_ERROR:
        //     return Object.assign({}, state.filter((error, i) => i !== action.index))
        case DISPLAY_ERROR:
            return Object.assign({}, state, {
                error: true,
                retryFn: action.data[0],
                retryParams: action.data[1]
            })
        case HIDE_ERROR:
            return Object.assign({}, state, {
                error: false
            })
        case RETRY_PREVIOUS_ACTION:
            return state
        default:
            return state
    }
}