import React, { Fragment, memo, useState, useEffect, useRef } from 'react'
import styled from 'styled-components'
import { privacyPolicy, termsAndConditions } from './../../data/settingsPage'
import SpatialNavigationWrapper from './../Shared/SpatialNavigationWrapper'
import { pauseSpatialNav, resumeSpatialNav, SpatialNavigation } from '../../utils/spatial-nav'
import {
  SETTINGS_PAGE_ID,
  SETTINGS_PAGE_PRIVACY_PAGE,
  SETTINGS_PAGE_TERMS_PAGE,
  KEYPRESS_ARROW_LEFT,
  KEYPRESS_ARROW_UP,
  KEYPRESS_ARROW_DOWN,
  SCROLL_AMOUNT
} from './../../utils/constants'

export const SideMenu = styled.div`
  max-width: 25rem;
  float: left;
  overflow: hidden;
  position: fixed;
`

export const MenuLink = styled.div`
  color: #eee;
  list-style: none;
  padding: 0.5rem 0.75rem;
  margin: 1.4rem;
  font-family: CircularStd;
  font-size: 1.125rem;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.1px;
  outline: none;
  &:hover {
    cursor: pointer;
  }
  &:focus {
    background-color: #eeeeee;
    color: #3c00b7;
  }
  &.selected {
    :after {
      content: '';
      position: absolute;
      bottom: ${p => p.topButton ? '80px' : '20px' };
      left: 50%;
      transform: translateX(-50%);
      background: ${p => p.theme.colors.primaryLight};
      height: 0.125rem;
      border-radius: 10px;
      width: 0.75rem;
    }
  }
`

export const DataContainer = styled.div`
  height: calc(100vh - 9.63rem);
  opacity: 0.6;
  font-family: CircularStd;
  font-size: 1.125rem;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.44;
  letter-spacing: 0.2px;
  color: #eee;
  overflow: auto;
  margin-right: 9.75rem;
  padding-right: 3rem;
  width: calc(100% - 34.75rem);
  float: right;
  padding-bottom: 1.875rem;
  outline: none;
  a {
    color: #eee;
  }
  &:focus {
    opacity: 1;
  }
`
export const ShadowContainer = styled.div`
  position: absolute;
  bottom: 0;
  left: 25rem;
  height: 6.25rem;
  background: linear-gradient(to bottom, rgba(10, 2, 31, 0), #0a021f);
  background-repeat: no-repeat;
  z-index: 2;
  width: calc(100% - 34.75rem);
`

const SettingsPage = () => {
  const [selectedPage, setSelectedPage] = useState(SETTINGS_PAGE_PRIVACY_PAGE)
  const containerRef = useRef()
  const termsRef = useRef()
  const privacyRef = useRef()

  useEffect(() => {
    SpatialNavigation.focus(`${SETTINGS_PAGE_ID}`)
    return () => resumeSpatialNav()
  }, [])

  const togglePage = e => {
    if (e.target.id !== selectedPage) {
      containerRef.current.scrollTop = 0
      setSelectedPage(e.target.id)
    }
  }

  const highlightSelected = () => {
    if (selectedPage === SETTINGS_PAGE_PRIVACY_PAGE) {
      privacyRef.current.classList.add('selected')
    }
    else {
      termsRef.current.classList.add('selected')
    }
  }

  const unhighlightSelected = () => {
    termsRef.current.classList.remove('selected')
    privacyRef.current.classList.remove('selected')
  }

  const handleFocus = () => {
    pauseSpatialNav()
    highlightSelected()
  }

  const handleKeyPress = e => {
    e.preventDefault()

    const { key } = e
    let currentTop = containerRef.current.scrollTop
    if (key === KEYPRESS_ARROW_LEFT) {
      selectedPage === SETTINGS_PAGE_TERMS_PAGE && termsRef.current.focus()
      resumeSpatialNav()
    } else if (key === KEYPRESS_ARROW_UP) {
      containerRef.current.scrollTop = currentTop - SCROLL_AMOUNT
    } else if (key === KEYPRESS_ARROW_DOWN) {
      containerRef.current.scrollTop = currentTop + SCROLL_AMOUNT
    }
  }

  const returnPage = () => {
    switch (selectedPage) {
      case SETTINGS_PAGE_PRIVACY_PAGE:
        return privacyPolicy
      case SETTINGS_PAGE_TERMS_PAGE:
        return termsAndConditions
      default:
        return privacyPolicy
    }
  }

  return (
    <Fragment>
      <SideMenu>
        <MenuLink
          topButton
          id={SETTINGS_PAGE_PRIVACY_PAGE}
          className={`focusable-${SETTINGS_PAGE_ID} focusable`}
          onKeyUp={togglePage}
          ref={privacyRef}
        >
          Privacy Policy
        </MenuLink>
        <MenuLink
          id={SETTINGS_PAGE_TERMS_PAGE}
          className={`focusable-${SETTINGS_PAGE_ID} focusable`}
          onKeyUp={togglePage}
          ref={termsRef}
        >
          Terms of Use
        </MenuLink>
        {/* <MenuLink>Nielsen Measurement</MenuLink> */}
      </SideMenu>
      <DataContainer
        ref={containerRef}
        className={`focusable-${SETTINGS_PAGE_ID} focusable`}
        onFocus={handleFocus}
        onKeyDown={handleKeyPress}
        onBlur={unhighlightSelected}
      >
        <span
          className="text"
          dangerouslySetInnerHTML={{ __html: returnPage() }}
        />
      </DataContainer>
      <ShadowContainer />
    </Fragment>
  )
}

const SettingsPageWithNav = memo(() => {
  const NavWrapper = SpatialNavigationWrapper({
    previousComponentMap: {
      [SETTINGS_PAGE_ID]: 'nav-bar'
    },
    nextComponentMap: {
      'nav-bar': SETTINGS_PAGE_ID
    }
  })
  return (
    <NavWrapper>
      <SettingsPage navId={`${SETTINGS_PAGE_ID}`} />
    </NavWrapper>
  )
})

export { SettingsPage }
export default SettingsPageWithNav
