import { replace } from 'connected-react-router'
import * as app from '../actions/app'

import { SPLASH_SCREEN_TIMEOUT } from '../utils/constants'

export const hideSplashScreen = cb => dispatch => {
  setTimeout(() => {
    dispatch(app.hideSplashScreen())
    cb()
  }, SPLASH_SCREEN_TIMEOUT)
}

export const onNavigate = data => dispatch => {
  dispatch(app.navigateToPage(data))
  dispatch(replace(data.pathURL))
}
