import React from 'react'
import StationCarousel from '../components/Shared/StationCarousel'
import CategorySection from '../components/CategorySection'

/*
    Create a component based on the given module object
    @param Object module object 
    @return Component 
*/
export const modulesFactory = (module, props) => {
  return () => {
    if (module.attributes.type === 'jumbo_category') {
      return <CategorySection module={module} {...props} />
    } else {
      return (
        <StationCarousel
          key={`station-carousel-${module.attributes.name}`}
          id={module.navId}
          headerText={module.attributes.name}
          type={module.attributes.type}
          stations={module.attributes ? module.attributes.destinations : []}
          isDiscoverPageCarousel={true}
        />
      )
    }
  }
}
