import React, { useEffect } from 'react'
import styled from 'styled-components'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogTitle from '@material-ui/core/DialogTitle'
import Slide from '@material-ui/core/Slide'

import DefaultButton from '../Shared/Buttons'
import {
  EXIT_APP_HEADER,
  EXIT_BUTTON_TEXT,
  STAY_BUTTON_TEXT
} from '../../utils/string-constants'
import SpatialNavigationWrapper from './../Shared/SpatialNavigationWrapper'
import $badgerReady from '../../utils/money-badger-wrapper'

import './exit-modal.scss'

const ExitModalContainer = styled(Dialog)`
  .exit-modal-content-container {
    background-color: ${p => p.theme.colors.windowModalBackground};
    max-width: 29rem;
    padding: 2.5rem 3.625rem;
  }
`

const ExitModalHeader = styled(DialogTitle)`
  h2 {
    font-family: ${p => p.theme.fonts.primary};
    color: ${p => p.theme.colors.primaryLight};
    font-size: 1.5rem;
    margin-top: 0;
    margin-bottom: 1rem;
    text-align: center;
    flex-grow: 1;
  }
`
function Transition(props) {
  return <Slide direction="up" {...props} />
}

const NavWrapper = SpatialNavigationWrapper({
  enableVerticalScroll: false
})

let $badger

const ExitModal = props => {
  const { id, open, focusComponent, blurComponent, closeModal } = props

  useEffect(() => {
    async function loadBadger() {
      $badger = await $badgerReady
    }
    loadBadger()
  }, [$badger])

  return (
    <ExitModalContainer
      onEntering={() => focusComponent(id)}
      onExiting={() => blurComponent(id)}
      open={open}
      TransitionComponent={Transition}
      transitionDuration={0}
      aria-labelledby="alert-dialog-slide-title"
      aria-describedby="alert-dialog-slide-description"
      PaperProps={{ classes: { root: 'exit-modal-content-container' } }}
    >
      <ExitModalHeader id="alert-dialog-slide-title">
        {EXIT_APP_HEADER}
      </ExitModalHeader>
      <DialogContent>
        <DefaultButton
          autoFocus
          className={`focusable-${props.navId} focusable`}
          onClick={closeModal}
          name="showExitModal"
          color="primary"
        >
          {STAY_BUTTON_TEXT}
        </DefaultButton>
        <DefaultButton
          className={`focusable-${props.navId} focusable default-selection`}
          onClick={() => {
            $badger.shutdown()
          }}
          color="primary"
        >
          {EXIT_BUTTON_TEXT}
        </DefaultButton>
      </DialogContent>
    </ExitModalContainer>
  )
}

const ExitModalWithNav = props => (
  <NavWrapper>
    <ExitModal navId="exit-modal" {...props} />
  </NavWrapper>
)

export { ExitModal }
export default ExitModalWithNav
