import React, { PureComponent, Fragment, memo } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components'

import {
  NavigateBrowseByCity,
  NavigateBrowseByGenre
} from '../../utils/navigation-map'
import {
  SEARCH_KEYPAD_ID,
  SEARCH_NAV_ID,
  SEARCH_BY_KEYWORKD_RESULTS_ID
} from './../../utils/constants'
import { focusOnDefaultElement } from '../../utils/spatial-nav'

import DefaultButton from '../Shared/Buttons'
import StationCarousel from '../Shared/StationCarousel'
import SpatialNavigationWrapper from '../Shared/SpatialNavigationWrapper'

import KeypadStaticWithNav from '../Keypad/Keypad'

import * as stationActionCreator from '../../asyncActionCreators/stations'
import * as asyncAppAction from '../../asyncActionCreators/app'

import MagnifyingGlass from './../../assets/search.svg'
import MagnifyingGlassNoResults from './../../assets/search_no_results.svg'

const NavigateToSearchKeyword = () => {
  focusOnDefaultElement(SEARCH_KEYPAD_ID)
}

const NavButton = styled(DefaultButton)`
  border-color: transparent;
  background-color: ${p => p.theme.colors.btnBackgroundActive};
  color: white;
  position: relative;
  margin-top: 0;
  margin-bottom: 0;
  width: 20.75rem;

  &.selected {
    :after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background: ${p => p.theme.colors.primaryLight};
      height: 0.125rem;
      border-radius: 10px;
      width: 0.75rem;
    }
  }
`

const SearchNav = ({ navId, onNavigate }) => {
  return (
    <Fragment>
      <NavButton
        onClick={NavigateToSearchKeyword}
        className={`focusable-${navId} selected`}
      >
        Search by Keyword
      </NavButton>
      <NavButton
        onClick={() => onNavigate(NavigateBrowseByCity)}
        className={`focusable-${navId}`}
      >
        Browse by City
      </NavButton>
      <NavButton
        onClick={() => onNavigate(NavigateBrowseByGenre)}
        className={`focusable-${navId}`}
      >
        Browse By Genre
      </NavButton>
    </Fragment>
  )
}

const SearchNavWithNav = memo(props => {
  const SearchWithNav = SpatialNavigationWrapper({
    enableHorizontalScroll: true,
    defaultElement: `.focusable-${SEARCH_NAV_ID}`,
    previousComponentMap: {
      [SEARCH_NAV_ID]: 'nav-bar'
    }
  })
  return (
    <div className="subnav">
      <SearchWithNav>
        <SearchNav navId={SEARCH_NAV_ID} {...props} />
      </SearchWithNav>
    </div>
  )
})

class SearchByKeywordComponent extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      searchValue: ''
    }
  }

  handleInputChange = e => {
    const { searchValue } = this.state
    const value = e.target.value
    const newVal = searchValue + value
    this.setState({ searchValue: newVal }, () => {
      this.getSearchResults()
    })
  }

  handleDeleteClick = e => {
    let newVal = this.state.searchValue
    newVal = newVal.substring(0, newVal.length - 1)

    this.setState({ searchValue: newVal }, () => {
      this.getSearchResults()
    })
  }

  handleSpaceClick = e => {
    let newVal = this.state.searchValue
    newVal = newVal + ' '

    this.setState({ searchValue: newVal }, () => {
      this.getSearchResults()
    })
  }

  handleClearClick = e => {
    let newVal = ''

    this.setState({ searchValue: newVal }, () => {
      this.getSearchResults()
    })
  }

  getSearchResults = () => {
    const { searchValue } = this.state
    const { searchResults } = this.props

    if (searchValue && searchValue.length > 1) {
      searchResults(searchValue)
    }
  }

  getSearchResultsHeader(searchValue) {
    return searchValue ? (
      <span>Results for: {searchValue}</span>
    ) : (
      <span>Start Typing...</span>
    )
  }

  getSearchResultsBody = (searchValue, searchStations) => {
    const CarouselWithNav = SpatialNavigationWrapper({
      enableHorizontalScroll: true
    })
    if (searchValue.length < 2) {
      return (
        <img
          src={MagnifyingGlass}
          alt="magnifying glass"
          className="magnifying-glass"
        />
      )
    } else if (searchValue.length >= 2 && searchStations.length === 0) {
      return (
        <div className="no-result-container">
          <img
            src={MagnifyingGlassNoResults}
            alt="magnifying glass no results"
            className="magnifying-glass"
          />
          <div className="no-results">
            No Search Results found for '{searchValue}' <br />
            Please try another keyword.
          </div>
        </div>
      )
    } else {
      return (
        <CarouselWithNav>
          <StationCarousel
            key="search-results"
            id="search-results"
            headerText=""
            type="square"
            stations={searchStations}
            navId={SEARCH_BY_KEYWORKD_RESULTS_ID}
          />
        </CarouselWithNav>
      )
    }
  }
  render() {
    const {
      match: { url },
      onNavigate
    } = this.props

    return (
      <Fragment>
        <SearchNavWithNav url={url} onNavigate={onNavigate} />
        <div className="search-container" key="search-container">
          <div
            className="search-results"
            key="search-results"
            navId={SEARCH_KEYPAD_ID}
          >
            {this.getSearchResultsHeader(this.state.searchValue)}
            {this.getSearchResultsBody(
              this.state.searchValue,
              this.props.searchStations
            )}
          </div>
          <KeypadStaticWithNav
            onKeyPress={this.handleInputChange}
            spacialNavSelector={`.focusable-key`}
            onDeleteKeyPress={this.handleDeleteClick}
            onSpaceKeyPress={this.handleSpaceClick}
            onClearKeyPress={this.handleClearClick}
            navId={SEARCH_KEYPAD_ID}
          />
        </div>
      </Fragment>
    )
  }
}

const mapStateToProps = state => {
  return {
    searchStations: state.stations.searchStations
  }
}

const mapDispatchToProps = dispatch => {
  return {
    searchResults: data =>
      dispatch(stationActionCreator.requestStationsBySearchTerm(data)),
    onNavigate: data => dispatch(asyncAppAction.onNavigate(data))
  }
}

const SearchByKeyword = connect(
  mapStateToProps,
  mapDispatchToProps
)(SearchByKeywordComponent)

export default SearchByKeyword
export { SearchByKeywordComponent }
