import React, { Fragment, useEffect } from 'react'

import { Switch, Route } from 'react-router-dom'
import { connect } from 'react-redux'

import { SEARCH_NAV_ID } from './../../utils/constants'

import { focusOnDefaultElement } from '../../utils/spatial-nav'

import * as marketActionCreators from '../../asyncActionCreators/market'
import SearchByKeyword from '../SearchByKeyword'
import SearchByCity from '../SearchByCity'
import GenresPage from '../GenresPage'
import './search-page.scss'

const SearchPageComponent = ({ onNavigate, match: { url } }) => {
  useEffect(() => {
    focusOnDefaultElement(SEARCH_NAV_ID)
  }, [])
  return (
    <Fragment>
      <Switch>
        <Route exact path={`${url}`} component={SearchByKeyword} />
        <Route path={`${url}/genre`} component={GenresPage} />
        <Route path={`${url}/market`} component={SearchByCity} />
      </Switch>
    </Fragment>
  )
}

const mapDispatchToProps = dispatch => {
  return {
    requestAllMarkets: () => dispatch(marketActionCreators.requestAllMarkets())
  }
}

const SearchPage = connect(
  null,
  mapDispatchToProps
)(SearchPageComponent)

export { SearchPageComponent }
export default SearchPage
