import styled, { keyframes } from 'styled-components'

const scroll = keyframes`
0% {
  transform: translateX(0%);
}
8% {
  transform: translateX(0%);
}
100% {
  transform: translateX(-300%);
}
`

const CardLabelHeader = styled.h4`
  font-size: 1.375rem;
  font-family: ${p => p.theme.fonts.bold};
  margin: 0;
  margin-bottom: 0.4375rem;
  line-height: 1;
  color: ${p => p.theme.colors.primaryLight};

  &:not(:focus) {
    height: 1.5rem;
  }

  &.scroll {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block;
    width: 100%;
  }
`
const StationImage = styled.div.attrs(({ imgUrl, className }) => ({
  style: {
    backgroundImage: `${
      className === 'circle'
        ? `linear-gradient(
      to bottom,
      rgba(12, 3, 31, 0),
      rgba(0, 0, 0, 0.25) 52%,
      rgba(0, 0, 0, 0)
    ), url('${imgUrl}')`
        : `url('${imgUrl}')`
    }`
  }
}))`
  height: 10.75rem;
  width: 10.75rem;
  background: center/contain no-repeat;
  outline: none;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.8);

  &.circle {
    border-radius: 50%;
    display: flex;
  }
`
const CardContainer = styled.button`
  width: 10.75rem;
  position: relative;

  display: inline-block;
  margin-right: 1rem;
  padding: 0;
  border: 0;
  background-color: inherit;
  text-align: left;
  font-size: 20px;

  &:focus {
    outline: none;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.8);
    color: red;

    ${StationImage}{
      box-shadow: none;
    }

    .card-content {
      padding: 0.75rem 0.625rem;

      ${CardLabelHeader}{
        margin-bottom: 0;
        font-size: 20px;
      }

      .scroll {
        padding-right: 80%;
        text-overflow: clip;
        overflow: visible;
        width: max-content;
        animation: ${scroll} 15s linear;
      }
    }
    &.square:before{
      border-bottom: 0.25rem solid ${p => p.theme.colors.primaryLight}
      background-color: ${p => p.theme.colors.primaryDark};
      transform-origin: center top;
    }
  }

  &.circle {
    border-radius: 50%;
    transform: scale(0.89);
    &:focus {
      transform: scale(1.11);
    }
  }
  &.square:before {
    z-index: -1;
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    transform-origin: 0 0;
    -webkit-transition: all 0.1s;
  }

  &:focus {
   
  }
`

const ViewAllImage = styled(StationImage)`
  background-image: linear-gradient(
    ${p => p.theme.colors.primaryAccent},
    ${p => p.theme.colors.primaryDark}
  );

  display: flex;
`

const ViewAllWavesImage = styled.img`
  margin: auto;
  width: 6.75rem;
`

const CardContentText = styled.p`
  font-size: 1.125rem;
  margin: 0;
  font-family: ${p => p.theme.fonts.primary};
  color: ${p => p.theme.colors.secondaryTitleColor};
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  &.circle {
    margin: auto;
    text-align: center;
    white-space: normal;
  }
`

const StationCardContent = styled.div`
  color: ${p => p.theme.colors.secondaryLight};
  border: none;
  padding-top: 6px;
  overflow: hidden;
  white-space: nowrap;
`

export {
  StationCardContent,
  CardContentText,
  CardLabelHeader,
  ViewAllWavesImage,
  ViewAllImage,
  StationImage,
  CardContainer
}
